// 5-tier difficulty system
export type Difficulty = 'very-easy' | 'easy' | 'medium' | 'hard' | 'very-hard'

// Ordered difficulty levels for progression
const DIFFICULTIES: Difficulty[] = [
	'very-easy',
	'easy',
	'medium',
	'hard',
	'very-hard',
]

// All available songs (25 total - 5 per difficulty)
export const songLibrary: Song[] = [
	// Very Easy (5)
	twinkleStar,
	maryHadALittleLamb,
	hotCrossBuns,
	jingleBells,
	rowRowRow,
	// Easy (5)
	odeToJoy,
	whenTheSaints,
	ohSusanna,
	londonBridge,
	happyBirthday,
	// Medium (5)
	canonInD,
	entertainer,
	minuetInG,
	turkishMarch,
	greensleeves,
	// Hard (5)
	furElise,
	moonlightSonata,
	hungarianDance,
	mountainKing,
	carmenHabanera,
	// Very Hard (5)
	electronicBeat,
	neonPulse,
	digitalStorm,
	cyberChase,
	synthWave,
]

// Songs organized by 5-tier difficulty (5 songs each)
export const songsByDifficulty: Record<Difficulty, Song[]> = {
	'very-easy': [
		twinkleStar,
		maryHadALittleLamb,
		hotCrossBuns,
		jingleBells,
		rowRowRow,
	],
	easy: [odeToJoy, whenTheSaints, ohSusanna, londonBridge, happyBirthday],
	medium: [canonInD, entertainer, minuetInG, turkishMarch, greensleeves],
	hard: [
		furElise,
		moonlightSonata,
		hungarianDance,
		mountainKing,
		carmenHabanera,
	],
	'very-hard': [electronicBeat, neonPulse, digitalStorm, cyberChase, synthWave],
}

// Get song for a specific level (auto-progression with random selection)
// Level 1 = very-easy, Level 2 = easy, Level 3 = medium, Level 4 = hard, Level 5 = very-hard
// Level 6+ = cycle repeats with speed increase
export function getSongForLevel(level: number): {
	song: Song
	cycle: number
	speedMultiplier: number
	difficulty: Difficulty
} {
	// Calculate which cycle we're on (5 levels per cycle)
	const cycle = Math.floor((level - 1) / 5)

	// Calculate difficulty index (0-4) within current cycle
	const difficultyIndex = (level - 1) % 5
	const difficulty = DIFFICULTIES[difficultyIndex]

	// Get songs for this difficulty and pick one randomly
	const songsInTier = songsByDifficulty[difficulty]
	const randomIndex = Math.floor(Math.random() * songsInTier.length)
	const song = songsInTier[randomIndex]

	// Speed multiplier increases each cycle
	// Cycle 0: 1.0x, Cycle 1: 1.1x, Cycle 2: 1.2x, etc.
	const speedMultiplier = 1.0 + cycle * 0.1

	return {
		song,
		cycle,
		speedMultiplier,
		difficulty,
	}
}

// Get song by ID
export function getSongById(id: string): Song | undefined {
	return songLibrary.find(s => s.metadata.id === id)
}

// Get all songs
export function getAllSongs(): Song[] {
	return songLibrary
}

// Get song metadata list (for UI display)
export function getSongList(): Array<{
	id: string
	title: string
	artist: string
	difficulty: Difficulty
}> {
	return songLibrary.map(s => ({
		id: s.metadata.id,
		title: s.metadata.title,
		artist: s.metadata.artist,
		difficulty: s.metadata.difficulty,
	}))
}

// Re-export types
export type { Song, MelodicNote, SongMetadata } from './songTypes'
