'use client'

import React, { useState, useCallback, useRef, lazy, Suspense } from 'react'
import { GameHeader } from '../ui/GameHeader'
import { GameOverlay } from '../ui/GameOverlay'
import { isMobile } from '../../../../utils/functions'
import { PenaltyShootoutProps, ShotResult } from './types'

// Lazy import for Phaser game
const PenaltyShootoutCore = lazy(() => import('./game'))

const LoadingFallback = () => (
	<div className="game-loading">
		<div>Cargando...</div>
	</div>
)

const PenaltyShootoutGame: React.FC<PenaltyShootoutProps> = ({
	lives = 5,
	setGameOver,
	setPointsEarned,
	brandColor = '#1a472a',
	skin,
	gameTitle = 'PENALES',
	brandLogo,
}) => {
	const containerRef = useRef<HTMLDivElement>(null)

	// Game state
	const [score, setScore] = useState(0)
	const [round, setRound] = useState(1)
	const [isGameStarted, setIsGameStarted] = useState(false)
	const [isGameOver, setIsGameOver] = useState(false)
	const [quitGame, setQuitGame] = useState(false)
	const [gameResult, setGameResult] = useState<{
		goals: number
		score: number
		won: boolean
	} | null>(null)

	const isMobileDevice = isMobile()

	const startGame = useCallback(() => {
		if (!isGameStarted) {
			setIsGameStarted(true)
			setIsGameOver(false)
			setScore(0)
			setRound(1)
			setQuitGame(false)
			setGameResult(null)
		}
	}, [isGameStarted])

	const cancelQuit = () => {
		setQuitGame(false)
	}

	const handleQuitConfirm = () => {
		setPointsEarned(score)
		setGameOver(true)
		setIsGameStarted(false)
		setIsGameOver(true)
	}

	const handleScoreUpdate = useCallback((newScore: number) => {
		setScore(newScore)
	}, [])

	const handleRoundComplete = useCallback(
		(roundNum: number, _result: ShotResult) => {
			setRound(roundNum + 1)
		},
		[]
	)

	const handleGameOverFromPhaser = useCallback(
		(finalGoals: number, totalScore: number) => {
			setIsGameOver(true)
			setGameResult({
				goals: finalGoals,
				score: totalScore,
				won: finalGoals >= Math.ceil(lives / 2),
			})
		},
		[lives]
	)

	const handleContinue = () => {
		setPointsEarned(score)
		setGameOver(true)
	}

	return (
		<div
			className="game-container"
			style={{ backgroundColor: brandColor, maxWidth: '32rem', margin: '0 auto' }}
			ref={containerRef}
		>
			{/* Game Header - Score, Lives (shots remaining), and Quit */}
			<GameHeader
				score={score}
				lives={lives - round + 1}
				brandColor={brandColor}
				isMobile={isMobileDevice}
				onQuit={() => setQuitGame(true)}
				showQuitButton={isGameStarted && !isGameOver}
				isQuitting={quitGame}
			/>

			{/* Game Canvas Area */}
			<div className="game-board">
				<div
					className="game-board-inner"
					style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', aspectRatio: '400/600', maxHeight: '70vh' }}
				>
					{/* Start Screen */}
					{!isGameStarted && !isGameOver && (
						<GameOverlay
							type="custom"
							brandColor={brandColor}
							onAction={startGame}
							actionText=" JUGAR"
							showPressToStart={false}
							customContent={
								<div style={{ textAlign: 'center' }}>
									<h1 style={{ fontSize: '2rem', fontWeight: 'bold', color: '#4ade80' }}>
										{gameTitle}
									</h1>
									{brandLogo && (
										<>
											<span style={{ fontSize: '0.75rem', color: 'white' }}>por:</span>
											<img
												src={brandLogo}
												alt="Brand Logo"
												style={{ margin: '0 auto', width: '12rem', height: 'auto', marginBottom: '1rem' }}
											/>
										</>
									)}
									<div style={{ color: '#d1d5db', fontSize: '0.875rem' }}>
										<p> Tira {lives} penales contra el portero</p>
										<p> Desliza desde el baln hacia la portera</p>
										<p> Curva tu tiro para engaar al portero</p>
										<p> Anota {Math.ceil(lives / 2)}+ goles para ganar</p>
									</div>
									<div style={{ fontSize: '0.75rem', color: '#9ca3af', marginTop: '0.75rem' }}>
										<p>Tiros curvos = +25 pts | Esquinas = +50 pts</p>
									</div>
								</div>
							}
						/>
					)}

					{/* Active Game */}
					{isGameStarted && !isGameOver && (
						<Suspense fallback={<LoadingFallback />}>
							<PenaltyShootoutCore
								onScoreUpdate={handleScoreUpdate}
								onRoundComplete={handleRoundComplete}
								onGameOver={handleGameOverFromPhaser}
								brandColor={brandColor}
								totalRounds={lives}
								skin={skin}
							/>
						</Suspense>
					)}

					{/* Game Over Screen */}
					{isGameOver && gameResult && (
						<GameOverlay
							type="custom"
							brandColor={brandColor}
							onAction={handleContinue}
							actionText="Continuar"
							customContent={
								<div style={{ textAlign: 'center' }}>
									<h1
										style={{
											fontSize: '2.5rem',
											fontWeight: 'bold',
											color: gameResult.won ? '#4ade80' : '#f87171'
										}}
									>
										{gameResult.won ? 'VICTORIA!' : 'DERROTA'}
									</h1>
									<div style={{ display: 'flex', justifyContent: 'center', gap: '0.5rem', margin: '1rem 0' }}>
										{Array.from({ length: lives }).map((_, i) => (
											<span
												key={i}
												style={{ fontSize: '1.5rem', opacity: i < gameResult.goals ? 1 : 0.3 }}
											>
												
											</span>
										))}
									</div>
									<p style={{ fontSize: '1.25rem', color: 'white' }}>
										{gameResult.goals} de {lives} goles
									</p>
									<p style={{ fontSize: '1.5rem', fontWeight: 'bold', color: '#facc15' }}>
										{gameResult.score} puntos
									</p>
									{gameResult.goals === lives && (
										<p style={{ fontSize: '1.125rem', color: '#fde047' }}>
											Puntuacin perfecta! 
										</p>
									)}
								</div>
							}
						/>
					)}

					{/* Quit Confirmation */}
					{quitGame && (
						<GameOverlay
							type="custom"
							title="Salir del Juego?"
							message={`Seguro? Registrars tus puntos actuales (${score}) y terminars la partida.`}
							brandColor={brandColor}
							customContent={
								<div className="game-quit-buttons">
									<button
										onClick={cancelQuit}
										className="game-quit-btn game-quit-btn-cancel"
									>
										 Cancelar
									</button>
									<button
										onClick={handleQuitConfirm}
										className="game-quit-btn game-quit-btn-confirm"
									>
										 Salir
									</button>
								</div>
							}
						/>
					)}
				</div>
			</div>
		</div>
	)
}

export default PenaltyShootoutGame
