import React from 'react'
import { createBoard } from '../game'
// Types
import type { Player } from './usePlayer'

export type BoardCell = [string | number, string]
export type Board = BoardCell[][]

export const useBoard = (player: Player, resetPlayer: () => void) => {
	const [board, setBoard] = React.useState(createBoard())
	const [rowsCleared, setRowsCleared] = React.useState(0)
	const [clearingRows, setClearingRows] = React.useState<number[]>([])

	React.useEffect(() => {
		if (!player.pos) return

		setRowsCleared(0)

		const sweepRows = (newBoard: Board): Board => {
			let rowsCleared = 0
			const rowsToRemove: number[] = []

			// First, identify which rows are full
			newBoard.forEach((row, index) => {
				if (row.every(cell => cell[0] !== 0)) {
					rowsToRemove.push(index)
					rowsCleared += 1
				}
			})

			// Set the rows to be cleared for animation
			if (rowsToRemove.length > 0) {
				setClearingRows(rowsToRemove)

				// Clear the animation after a delay
				setTimeout(() => {
					setClearingRows([])
				}, 300)
			}

			// Then remove them
			const updatedBoard = newBoard.reduce((acc, row, index) => {
				if (rowsToRemove.includes(index)) {
					// Aadir una nueva fila vaca al principio
					acc.unshift(
						new Array(newBoard[0].length).fill([0, 'clear']) as BoardCell[]
					)
				} else {
					// Mantener la fila actual si no est llena
					acc.push(row)
				}
				return acc
			}, [] as Board)

			setRowsCleared(rowsCleared)
			return updatedBoard
		}

		const updateBoard = (prevBoard: Board): Board => {
			// First flush the board
			// If it says "clear" but don't have a 0 it means that it's the players move and should be cleared
			const newBoard = prevBoard.map(
				row =>
					row.map(cell =>
						cell[1] === 'clear' ? [0, 'clear'] : cell
					) as BoardCell[]
			)

			// Then draw the tetromino
			player.tetromino.forEach((row, y) => {
				row.forEach((value, x) => {
					if (value !== 0) {
						newBoard[y + player.pos.y][x + player.pos.x] = [
							value,
							`${player.collided ? 'merged' : 'clear'}`,
						]
					}
				})
			})

			if (player.collided) {
				resetPlayer()
				return sweepRows(newBoard)
			}

			return newBoard
		}

		setBoard(prev => updateBoard(prev))
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [player.collided, player.pos?.x, player.pos?.y, player.tetromino])

	return { board, setBoard, rowsCleared, clearingRows }
}
