import { useCallback, useEffect, useRef, useState } from 'react'
import { flappyBirdAudio } from '../utils/audioManager'

interface IConfig {
	assets: {
		bird: HTMLImageElement
		background: HTMLImageElement
		foreground: HTMLImageElement
		pipeNorth: HTMLImageElement
		pipeSouth: HTMLImageElement
		coin: HTMLImageElement
		heart: HTMLImageElement
	}
	options: {
		delayFrameCount: number
		birdX: number
		gap: number
		upMovement: number
		gravity: number
		pointsPerObstacle: number
		lives: number
		gameTitle: string
		hideOverlays?: boolean
		isPaused?: boolean
		maxPoints?: number
	}
	functions: {
		setGameOver: (isOver: boolean) => void
		setPointsEarned: (points: number) => void
		onScoreUpdate?: (score: number) => void
		onLivesUpdate?: (lives: number) => void
	}
}

interface Pipe {
	x: number
	y: number
	passed: boolean
}

const PIPE_WIDTH = 52
const PIPE_GAP_REDUCTION_RATE = 20
const MIN_GAP = 350
const BIRD_WIDTH = 34
const BIRD_HEIGHT = 24
const COLLISION_PADDING = 8
const FPS = 60
const FRAME_TIME = 1000 / FPS

const useFlappyAnimation = (config: IConfig | null) => {
	const [score, setScore] = useState(0)
	const [currentLives, setCurrentLives] = useState(config?.options.lives || 3)
	const [countCollisions, setCountCollisions] = useState(0)
	const [gameStarted, setGameStarted] = useState(false)
	const [isGameOver, setIsGameOver] = useState(false)
	const [currentGap, setCurrentGap] = useState(config?.options.gap || 500)

	const isOnTheFloor = useRef(false)
	const delay = useRef(0)
	const birdY = useRef(150)
	const birdVelocity = useRef(0)
	const pipesArray = useRef<Pipe[]>([{ x: 288, y: 0, passed: false }])
	const lastFrameTime = useRef(0)
	const birdRotation = useRef(0)

	// Initialize audio on mount
	useEffect(() => {
		flappyBirdAudio.init()
		return () => {
			flappyBirdAudio.dispose()
		}
	}, [])

	const tap = useCallback(() => {
		if (!config) return

		if (!gameStarted) {
			start()
		} else if (gameStarted && !isGameOver) {
			moveUp()
		} else if (
			delay.current > config.options.delayFrameCount &&
			isOnTheFloor.current
		) {
			reset()
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [config, gameStarted, isGameOver])

	const moveUp = useCallback(() => {
		if (!config) return
		birdVelocity.current = -config.options.upMovement / 2.8
		flappyBirdAudio.playFlap()
	}, [config])

	const start = useCallback(() => {
		setGameStarted(true)
		setIsGameOver(false)
		setScore(0)
		setCurrentLives(config?.options.lives || 3)
		setCountCollisions(0)
		birdY.current = 150
		birdVelocity.current = 0
		birdRotation.current = 0
		pipesArray.current = [{ x: 288, y: 0, passed: false }]
		setCurrentGap(config?.options.gap || 500)
		delay.current = 0
	}, [config])

	const reset = useCallback(() => {
		birdY.current = 150
		birdVelocity.current = 0
		birdRotation.current = 0
		pipesArray.current = [{ x: 288, y: 0, passed: false }]
		delay.current = 0

		if (currentLives <= 0 || countCollisions >= (config?.options.lives || 3)) {
			start()
		} else {
			setGameStarted(true)
			setIsGameOver(false)
		}
	}, [currentLives, countCollisions, config, start])

	const collision = useCallback(() => {
		const newCollisions = countCollisions + 1
		const newLives = currentLives - 1

		setCountCollisions(newCollisions)
		setCurrentLives(newLives)
		config?.functions.onLivesUpdate?.(newLives)
		flappyBirdAudio.playCollision()

		if (newLives <= 0 || newCollisions >= (config?.options.lives || 3)) {
			setIsGameOver(true)
			config?.functions.setGameOver(true)
			config?.functions.setPointsEarned(score)
			flappyBirdAudio.playGameOver()
		} else {
			reset()
		}
	}, [countCollisions, currentLives, config, score, reset])

	const draw = useCallback(
		(context: CanvasRenderingContext2D) => {
			if (!config) return

			const currentTime = performance.now()
			const deltaTime = currentTime - lastFrameTime.current

			if (deltaTime < FRAME_TIME) return

			lastFrameTime.current = currentTime

			const canvas = context.canvas
			const {
				bird,
				foreground,
				background,
				pipeNorth,
				pipeSouth,
				coin,
				heart,
			} = config.assets
			const { gravity } = config.options

			// Calculate pipe movement speed based on level
			const currentLevel = Math.floor(score / 200) + 1
			const pipeMovement = isGameOver ? 0 : 2 + (currentLevel - 1) * 0.2
			const pipes = pipesArray.current

			context.clearRect(0, 0, canvas.width, canvas.height)
			context.drawImage(background, 0, 0)

			if (gameStarted && !isGameOver && !config.options.isPaused) {
				birdVelocity.current += gravity / 6
				birdVelocity.current = Math.min(birdVelocity.current, 8)
				birdY.current += birdVelocity.current

				birdRotation.current = Math.min(
					Math.max(birdVelocity.current * 3, -25),
					90
				)

				for (let i = pipes.length - 1; i >= 0; i--) {
					if (pipes[i].x < -PIPE_WIDTH) {
						pipes.splice(i, 1)
						continue
					}

					context.drawImage(pipeNorth, pipes[i].x, pipes[i].y)
					context.drawImage(pipeSouth, pipes[i].x, pipes[i].y + currentGap)

					pipes[i].x -= pipeMovement

					// Generate new pipe when the last pipe reaches a certain position
					if (
						i === pipes.length - 1 &&
						pipes[i].x <= canvas.width - 140 &&
						!isGameOver
					) {
						// Increase variation based on score - more variety as game progresses
						const difficultyFactor = Math.min(score / 100, 1.5)
						const minY = -pipeNorth.height + 80
						const maxY = canvas.height - foreground.height - currentGap - 80

						// Add more dramatic position changes between consecutive pipes
						const lastPipeY = pipes[i].y
						const baseY = Math.floor(Math.random() * (maxY - minY)) + minY

						// Ensure some minimum difference from last pipe for variety
						let newY = baseY
						if (Math.abs(baseY - lastPipeY) < 50) {
							// Force a bigger change if pipes are too similar
							newY = baseY + (Math.random() > 0.5 ? 60 : -60) * difficultyFactor
							// Clamp to valid range
							newY = Math.max(minY, Math.min(maxY, newY))
						}

						// Add random spacing between pipes - less spacing as difficulty increases
						const baseSpacing = 180 - Math.floor(score / 100) * 5
						const spacing =
							Math.floor(Math.random() * 30) + Math.max(150, baseSpacing)

						pipes.push({
							x: pipes[i].x + spacing,
							y: newY,
							passed: false,
						})
					}

					if (
						!pipes[i].passed &&
						pipes[i].x + PIPE_WIDTH < config.options.birdX &&
						!isGameOver
					) {
						pipes[i].passed = true
						// Calculate points based on level
						const currentLevel = Math.floor(score / 200) + 1
						const pointsEarned =
							config.options.pointsPerObstacle + (currentLevel - 1) * 5
						const newScore = score + pointsEarned
						flappyBirdAudio.playScore()

						// Check if maxPoints is reached
						if (
							config.options.maxPoints &&
							config.options.maxPoints > 0 &&
							newScore >= config.options.maxPoints
						) {
							// Set score to exactly maxPoints
							setScore(config.options.maxPoints)
							config.functions.onScoreUpdate?.(config.options.maxPoints)
							// Trigger game over since max points reached
							setIsGameOver(true)
							config.functions.setGameOver(true)
							config.functions.setPointsEarned(config.options.maxPoints)
						} else {
							setScore(newScore)
							config.functions.onScoreUpdate?.(newScore)

							// Gradual gap reduction for smoother difficulty curve
							const gapReduction =
								Math.floor(newScore / PIPE_GAP_REDUCTION_RATE) * 6
							const newGap = Math.max(
								config.options.gap - gapReduction,
								MIN_GAP
							)
							setCurrentGap(newGap)
						}
					}

					const hasHitFloor =
						birdY.current + BIRD_HEIGHT >=
						canvas.height - foreground.height + 20
					const hasHitCeiling = birdY.current <= 0

					// More precise collision boxes
					const birdLeft = config.options.birdX + COLLISION_PADDING
					const birdRight =
						config.options.birdX + BIRD_WIDTH - COLLISION_PADDING
					const birdTop = birdY.current + COLLISION_PADDING
					const birdBottom = birdY.current + BIRD_HEIGHT - COLLISION_PADDING

					// Pipe collision box with slight padding for fairness
					const pipeLeft = pipes[i].x + 2
					const pipeRight = pipes[i].x + PIPE_WIDTH - 2
					const pipeTopBottom = pipes[i].y + pipeNorth.height - 5
					const pipeBottomTop = pipes[i].y + currentGap + 5

					// Only check collision if bird is actually overlapping with pipe
					const hasHitPipe =
						birdRight > pipeLeft &&
						birdLeft < pipeRight &&
						(birdTop < pipeTopBottom || birdBottom > pipeBottomTop)

					isOnTheFloor.current = hasHitFloor

					if (hasHitPipe || hasHitFloor || hasHitCeiling) {
						collision()
					}
				}

				context.save()
				context.translate(
					config.options.birdX + BIRD_WIDTH / 2,
					birdY.current + BIRD_HEIGHT / 2
				)
				context.rotate((birdRotation.current * Math.PI) / 180)
				context.drawImage(bird, -BIRD_WIDTH / 2, -BIRD_HEIGHT / 2)
				context.restore()
			}

			context.drawImage(foreground, 0, canvas.height - foreground.height + 20)

			if (isGameOver && !config.options.hideOverlays) {
				delay.current++

				context.fillStyle = 'rgba(0, 0, 0, 0.5)'
				context.fillRect(0, 0, canvas.width, canvas.height)

				context.font = "48px 'Tahoma'"
				context.fillStyle = '#fff'
				context.strokeStyle = '#000'
				context.lineWidth = 4
				context.strokeText('Game Over', 24, 260)
				context.fillText('Game Over', 24, 260)

				if (isOnTheFloor.current) {
					context.font = "bold 16px 'Tahoma'"
					context.strokeStyle = '#000'
					context.lineWidth = 3
					context.strokeText('Haz clic para comenzar', 54, 460)
					context.fillStyle = '#fff'
					context.fillText('Haz clic para comenzar', 54, 460)
				}
			} else {
				if (!gameStarted) {
					delay.current++

					if (!config.options.hideOverlays) {
						context.fillStyle = 'rgba(0, 0, 0, 0.3)'
						context.fillRect(0, 0, canvas.width, canvas.height)

						context.font = "42px 'Tahoma'"
						context.fillStyle = '#fff'
						context.strokeStyle = '#000'
						context.lineWidth = 4
						const titleWidth = context.measureText(
							config.options.gameTitle
						).width
						const titleX = (canvas.width - titleWidth) / 2
						context.strokeText(config.options.gameTitle, titleX, 160)
						context.fillText(config.options.gameTitle, titleX, 160)
					}

					// Always show click to start message, with pulsing effect
					const alpha = Math.abs(Math.sin(delay.current * 0.03))
					context.font = "bold 18px 'Tahoma'"
					context.strokeStyle = '#000'
					context.lineWidth = 3
					context.globalAlpha = alpha
					context.strokeText('Haz clic para comenzar', 44, 460)
					context.fillStyle = '#fff'
					context.fillText('Haz clic para comenzar', 44, 460)
					context.globalAlpha = 1
				}

				if (gameStarted && !config.options.hideOverlays) {
					context.drawImage(coin, 20, 20, 32, 32)
					context.fillStyle = '#fff'
					context.strokeStyle = '#000'
					context.lineWidth = 3
					context.font = "bold 32px 'Tahoma'"
					context.strokeText(`${score}`, 70, 50)
					context.fillText(`${score}`, 70, 50)

					context.drawImage(heart, 200, 20, 32, 32)
					context.strokeText(`${currentLives}`, 250, 50)
					context.fillText(`${currentLives}`, 250, 50)

					// Show difficulty level
					const difficultyLevel = Math.floor(score / 200) + 1
					if (difficultyLevel > 1) {
						context.font = "bold 14px 'Tahoma'"
						context.strokeStyle = '#000'
						context.lineWidth = 2
						context.fillStyle = '#ff0'
						const levelText = `Nivel ${difficultyLevel}`
						context.strokeText(levelText, 20, 80)
						context.fillText(levelText, 20, 80)
					}
				}
			}
		},
		[
			config,
			gameStarted,
			isGameOver,
			score,
			currentLives,
			currentGap,
			collision,
		]
	)

	useEffect(() => {
		if (currentLives <= 0 || countCollisions >= (config?.options.lives || 3)) {
			setIsGameOver(true)
			config?.functions.setGameOver(true)
			config?.functions.setPointsEarned(score)
		}
	}, [countCollisions, currentLives, config, score])

	return [tap, draw] as const
}

export default useFlappyAnimation
