// Lane Assignment Algorithms for Melodic Song System
// Converts raw melody notes to playable 4-lane patterns

import type { MelodicNote, LaneAssignmentOptions } from '../songs/songTypes'

// MIDI note number to pitch name conversion
const NOTE_NAMES = [
	'C',
	'C#',
	'D',
	'D#',
	'E',
	'F',
	'F#',
	'G',
	'G#',
	'A',
	'A#',
	'B',
]

export function pitchToMidi(pitch: string): number {
	// Parse pitch like "C4", "F#5", "Bb3"
	const match = pitch.match(/^([A-G])([#b]?)(\d+)$/)
	if (!match) return 60 // Default to middle C

	const [, note, accidental, octaveStr] = match
	const octave = parseInt(octaveStr, 10)

	let noteIndex = NOTE_NAMES.indexOf(note)
	if (accidental === '#') noteIndex++
	if (accidental === 'b') noteIndex--

	// MIDI note number: C4 = 60
	return (octave + 1) * 12 + noteIndex
}

export function midiToPitch(midi: number): string {
	const octave = Math.floor(midi / 12) - 1
	const noteIndex = midi % 12
	return `${NOTE_NAMES[noteIndex]}${octave}`
}

// Transpose a pitch by a number of semitones
export function transposePitch(pitch: string, semitones: number): string {
	const midi = pitchToMidi(pitch)
	return midiToPitch(midi + semitones)
}

// Transpose a pitch by octaves
export function transposeOctave(pitch: string, octaves: number): string {
	return transposePitch(pitch, octaves * 12)
}

interface PitchRange {
	min: number
	max: number
	range: number
}

function analyzePitchRange(notes: MelodicNote[]): PitchRange {
	const midiValues = notes.map(n => pitchToMidi(n.pitch))
	const min = Math.min(...midiValues)
	const max = Math.max(...midiValues)
	return { min, max, range: max - min }
}

/**
 * Pitch-Range Mapping Strategy
 * Divides the song's pitch range into 4 zones and assigns each to a lane.
 * Adjacent pitches map to adjacent lanes, creating natural hand movement.
 */
function assignByPitchRange(notes: MelodicNote[]): MelodicNote[] {
	if (notes.length === 0) return []

	const { min, range } = analyzePitchRange(notes)

	// Handle songs with very small range (all same pitch or close)
	const effectiveRange = Math.max(range, 4)
	const zoneSize = effectiveRange / 4

	return notes.map(note => {
		const midi = pitchToMidi(note.pitch)
		const relativePosition = midi - min

		// Map to lane 0-3 based on position in range
		const rawLane = Math.floor(relativePosition / zoneSize)
		const lane = Math.max(0, Math.min(3, rawLane)) as 0 | 1 | 2 | 3

		return { ...note, lane }
	})
}

/**
 * Complexity-Aware Mapping Strategy
 * Optimizes for playability by avoiding impossible patterns and
 * ensuring reasonable travel distance between consecutive notes.
 */
function assignByComplexity(
	notes: MelodicNote[],
	maxSimultaneous: number = 2
): MelodicNote[] {
	if (notes.length === 0) return []

	// First pass: assign by pitch range
	let assignedNotes = assignByPitchRange(notes)

	// Second pass: resolve simultaneous note conflicts
	const notesByBeat = new Map<number, MelodicNote[]>()
	for (const note of assignedNotes) {
		const beatKey = Math.round(note.beat * 1000) // Round to avoid floating point issues
		if (!notesByBeat.has(beatKey)) {
			notesByBeat.set(beatKey, [])
		}
		notesByBeat.get(beatKey)!.push(note)
	}

	// Resolve conflicts where too many notes share the same beat
	const resolvedNotes: MelodicNote[] = []
	for (const [, beatNotes] of Array.from(notesByBeat.entries())) {
		if (beatNotes.length <= maxSimultaneous) {
			// Ensure unique lanes for simultaneous notes
			const usedLanes = new Set<number>()
			for (const note of beatNotes) {
				let lane = note.lane
				while (usedLanes.has(lane)) {
					lane = ((lane + 1) % 4) as 0 | 1 | 2 | 3
				}
				usedLanes.add(lane)
				resolvedNotes.push({ ...note, lane })
			}
		} else {
			// Too many simultaneous notes - keep only the most important ones
			// (highest and lowest pitches tend to be most melodically significant)
			const sorted = [...beatNotes].sort(
				(a, b) => pitchToMidi(a.pitch) - pitchToMidi(b.pitch)
			)
			const kept = [sorted[0], sorted[sorted.length - 1]]
			if (maxSimultaneous > 2 && sorted.length > 2) {
				kept.push(sorted[Math.floor(sorted.length / 2)])
			}

			const usedLanes = new Set<number>()
			for (const note of kept.slice(0, maxSimultaneous)) {
				let lane = note.lane
				while (usedLanes.has(lane)) {
					lane = ((lane + 1) % 4) as 0 | 1 | 2 | 3
				}
				usedLanes.add(lane)
				resolvedNotes.push({ ...note, lane })
			}
		}
	}

	// Sort by beat
	return resolvedNotes.sort((a, b) => a.beat - b.beat)
}

/**
 * Main lane assignment function
 * Takes raw notes (potentially without lane assignments) and returns
 * notes with optimized lane assignments for playability.
 */
export function assignLanes(
	notes: MelodicNote[],
	options: LaneAssignmentOptions = { strategy: 'pitch-range' }
): MelodicNote[] {
	const { strategy, maxSimultaneous = 2 } = options

	switch (strategy) {
		case 'pitch-range':
			return assignByPitchRange(notes)
		case 'complexity-aware':
			return assignByComplexity(notes, maxSimultaneous)
		default:
			return assignByPitchRange(notes)
	}
}

/**
 * Validate that a song's lane assignments are playable
 * Returns issues found, or empty array if valid
 */
export function validateLaneAssignments(notes: MelodicNote[]): string[] {
	const issues: string[] = []

	// Check for too many simultaneous notes
	const notesByBeat = new Map<number, MelodicNote[]>()
	for (const note of notes) {
		const beatKey = Math.round(note.beat * 1000)
		if (!notesByBeat.has(beatKey)) {
			notesByBeat.set(beatKey, [])
		}
		notesByBeat.get(beatKey)!.push(note)
	}

	for (const [beat, beatNotes] of Array.from(notesByBeat.entries())) {
		if (beatNotes.length > 2) {
			issues.push(
				`Beat ${beat / 1000}: ${beatNotes.length} simultaneous notes (max recommended: 2)`
			)
		}

		// Check for duplicate lanes at same beat
		const lanes = beatNotes.map(n => n.lane)
		const uniqueLanes = new Set(lanes)
		if (lanes.length !== uniqueLanes.size) {
			issues.push(`Beat ${beat / 1000}: Multiple notes in same lane`)
		}
	}

	// Check for invalid lane values
	for (const note of notes) {
		if (note.lane < 0 || note.lane > 3) {
			issues.push(`Beat ${note.beat}: Invalid lane ${note.lane}`)
		}
	}

	return issues
}

/**
 * Balance lane distribution
 * Slightly adjusts assignments to ensure all lanes are used reasonably evenly
 */
export function balanceLaneDistribution(notes: MelodicNote[]): MelodicNote[] {
	if (notes.length < 8) return notes // Too few notes to balance

	// Count current distribution
	const laneCounts = [0, 0, 0, 0]
	for (const note of notes) {
		laneCounts[note.lane]++
	}

	const avgCount = notes.length / 4
	const threshold = avgCount * 0.5 // Allow 50% deviation

	// Find underused and overused lanes
	const underused: (0 | 1 | 2 | 3)[] = laneCounts
		.map((count, lane) => ({ lane: lane as 0 | 1 | 2 | 3, count }))
		.filter(l => l.count < avgCount - threshold)
		.map(l => l.lane)

	const overused: (0 | 1 | 2 | 3)[] = laneCounts
		.map((count, lane) => ({ lane: lane as 0 | 1 | 2 | 3, count }))
		.filter(l => l.count > avgCount + threshold)
		.map(l => l.lane)

	if (underused.length === 0 || overused.length === 0) return notes

	// Redistribute some notes from overused to underused lanes
	const result = [...notes]
	for (let i = 0; i < result.length && underused.length > 0; i++) {
		const note = result[i]
		if (overused.includes(note.lane)) {
			// Don't move if it would create same-lane conflict at same beat
			const sameBeatNotes = result.filter(
				n => Math.abs(n.beat - note.beat) < 0.01 && n !== note
			)
			const sameBeatLanes = new Set(sameBeatNotes.map(n => n.lane))

			const targetLane = underused.find(l => !sameBeatLanes.has(l)) as
				| (0 | 1 | 2 | 3)
				| undefined
			if (targetLane !== undefined) {
				result[i] = { ...note, lane: targetLane }
				laneCounts[note.lane]--
				laneCounts[targetLane]++

				// Update underused/overused lists
				if (laneCounts[targetLane] >= avgCount - threshold) {
					underused.splice(underused.indexOf(targetLane), 1)
				}
			}
		}
	}

	return result
}
