'use client'

import React, { useRef, useEffect, useState, useCallback } from 'react'
import FlappyBirdCore from './game'
import { GameHeader } from '../ui/GameHeader'
import { GameOverlay } from '../ui/GameOverlay'
import { isMobile } from '../../../../utils/functions'

interface FlappyBirdGameProps {
	initialLives: number
	setGameOver: (isOver: boolean) => void
	setPointsEarned: (points: number) => void
	brandColor?: string
	skin?: string
	gameTitle?: string
	maxPoints?: number
	brandLogo?: string
}

const FlappyBirdGame: React.FC<FlappyBirdGameProps> = ({
	initialLives,
	setGameOver,
	setPointsEarned,
	brandColor = '#00bfff',
	skin,
	gameTitle = 'FLAPPY BIRD',
	maxPoints,
	brandLogo,
}) => {
	const containerRef = useRef<HTMLDivElement>(null)
	const gameContainerRef = useRef<HTMLDivElement>(null)

	const [score, setScore] = useState(0)
	const [lives, setLives] = useState(initialLives)
	const [level, setLevel] = useState(1)
	const [showLevelUp, setShowLevelUp] = useState(false)
	const [levelUpText, setLevelUpText] = useState('')
	const [isPaused, setIsPaused] = useState(false)
	const [isGameStarted, setIsGameStarted] = useState(false)
	const [isGameOver, setIsGameOver] = useState(false)
	const [coreGameOver, setCoreGameOver] = useState(false)
	const [quitGame, setQuitGame] = useState(false)
	const [wasGamePaused, setWasGamePaused] = useState(false)
	const [, setWindowSize] = useState({ width: 0, height: 0 })

	const isMobileDevice = isMobile()

	// Handle window resize
	useEffect(() => {
		const handleResize = () => {
			setWindowSize({ width: window.innerWidth, height: window.innerHeight })
		}

		window.addEventListener('resize', handleResize)
		handleResize() // Initial call

		return () => window.removeEventListener('resize', handleResize)
	}, [])

	// Handle keyboard input
	useEffect(() => {
		const handleKeyPress = (e: KeyboardEvent) => {
			if (e.key === ' ' || e.key === 'Escape') {
				e.preventDefault()
				if (isGameStarted && !isGameOver) {
					togglePause()
				}
			}
		}

		window.addEventListener('keydown', handleKeyPress)
		return () => window.removeEventListener('keydown', handleKeyPress)
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [isGameStarted, isGameOver])

	const togglePause = useCallback(() => {
		if (isGameStarted && !isGameOver) {
			setIsPaused(prev => !prev)
		}
	}, [isGameStarted, isGameOver])

	const startGame = useCallback(() => {
		if (!isGameStarted) {
			setIsGameStarted(true)
			setIsGameOver(false)
			setCoreGameOver(false)
			setScore(0)
			setLives(initialLives)
			setQuitGame(false)
		}
	}, [isGameStarted, initialLives])

	const cancelQuit = () => {
		setQuitGame(false)
		setIsPaused(wasGamePaused)
	}

	const handleQuitConfirm = () => {
		setPointsEarned(score)
		setGameOver(true)
		setIsGameStarted(false)
		setIsGameOver(true)
		setCoreGameOver(true)
	}

	const handleGameOver = useCallback(
		(finalGameOver: boolean) => {
			if (finalGameOver && !isGameOver) {
				setCoreGameOver(true)
				setIsGameOver(true)
				setGameOver(true)
			}
		},
		[isGameOver, setGameOver]
	)

	const handlePointsEarned = useCallback(
		(points: number) => {
			setScore(points)
			setPointsEarned(points)
		},
		[setPointsEarned]
	)

	const handleScoreUpdate = useCallback(
		(newScore: number) => {
			setScore(newScore)
			// Update level based on score (every 100 points = new level)
			const newLevel = Math.floor(newScore / 100) + 1
			if (newLevel > level) {
				setLevel(newLevel)
				setLevelUpText(`Nivel ${newLevel}!`)
				setShowLevelUp(true)
				// Hide notification after 1.2 seconds
				setTimeout(() => setShowLevelUp(false), 800)
			}
		},
		[level]
	)

	// Calculate canvas scale
	const getCanvasScale = () => {
		const canvasWidth = 288
		const canvasHeight = 512

		const padding = isMobileDevice ? 16 : 32
		const headerHeight = 80
		const controlsHeight = isMobileDevice ? 160 : 0
		const extraMargin = 20

		const containerWidth = window.innerWidth - padding * 2
		const containerHeight =
			window.innerHeight - headerHeight - controlsHeight - extraMargin

		// Target a reasonable max height for the game
		const maxHeight = 600
		const effectiveHeight = Math.min(containerHeight, maxHeight)

		const scaleX = containerWidth / canvasWidth
		const scaleY = effectiveHeight / canvasHeight
		let scale = Math.min(scaleX, scaleY)

		if (isMobileDevice) {
			scale = Math.min(scale, 0.85)
			return Math.max(scale, 0.4)
		} else {
			return Math.min(scale, 0.9) // Max 90% scale on desktop
		}
	}

	return (
		<div
			className="game-container"
			style={{ backgroundColor: brandColor ? brandColor : '#111827' }}
			ref={containerRef}
		>
			{/* Game Header */}
			<GameHeader
				level={level}
				lives={lives}
				score={score}
				onPause={togglePause}
				isPaused={isPaused}
				brandColor={brandColor}
				isMobile={isMobileDevice}
				onQuit={() => {
					setQuitGame(true)
					setWasGamePaused(isPaused)
					setIsPaused(true)
				}}
				showQuitButton={isGameStarted}
				isQuitting={quitGame}
			/>

			{/* Game Canvas */}
			<div className="game-board">
				<div
					className="game-board-inner"
					style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '512px' }}
					ref={gameContainerRef}
				>
					{isGameStarted && !coreGameOver && (
						<div
							style={{
								transform: `scale(${getCanvasScale()})`,
								transformOrigin: 'center',
							}}
						>
							<FlappyBirdCore
								lives={initialLives}
								setGameOver={handleGameOver}
								setPointsEarned={handlePointsEarned}
								skin={skin}
								gameTitle="FLAPPY CAT"
								hideOverlays={true}
								onScoreUpdate={handleScoreUpdate}
								onLivesUpdate={setLives}
								isPaused={isPaused}
								maxPoints={maxPoints}
							/>
						</div>
					)}

					{/* Level Up Notification */}
					{showLevelUp && isGameStarted && !isGameOver && (
						<div className="game-notification" style={{ top: '25%' }}>
							<div className="game-notification-content" style={{ backgroundColor: '#eab308', color: 'black' }}>
								<p className="game-notification-text">{levelUpText}</p>
							</div>
						</div>
					)}

					{/* Game Overlays */}
					{!isGameStarted && (
						<GameOverlay
							type="custom"
							brandColor={brandColor}
							onAction={startGame}
							actionText=" JUGAR"
							showPressToStart={false}
							customContent={
								<div style={{ textAlign: 'center' }}>
									<h1 style={{ fontSize: '2.5rem', fontWeight: 'bold', color: '#38bdf8', marginBottom: '0.5rem' }}>
										{gameTitle}
									</h1>
									{brandLogo && (
										<>
											<span style={{ fontSize: '0.75rem', color: 'white' }}>por:</span>
											<img
												src={brandLogo}
												alt="Brand Logo"
												style={{ margin: '0 auto', width: '12rem', height: 'auto', marginBottom: '1rem' }}
											/>
										</>
									)}
									<div style={{ color: '#d1d5db', fontSize: '1rem' }}>
										<p> Toca para volar</p>
										<p> Evita los obstculos</p>
										<p> Gana puntos al pasar</p>
									</div>
									<div style={{ fontSize: '0.875rem', color: '#9ca3af', marginTop: '1rem' }}>
										<p>Usa la barra espaciadora o toca la pantalla</p>
									</div>
								</div>
							}
						/>
					)}

					{isPaused && !isGameOver && (
						<GameOverlay
							type="pause"
							brandColor={brandColor}
							onAction={togglePause}
						/>
					)}

					{isGameOver && (
						<GameOverlay
							type="gameOver"
							score={score}
							brandColor={brandColor}
						/>
					)}

					{/* Quit Confirmation Overlay */}
					{quitGame && (
						<GameOverlay
							type="custom"
							title="Salir del Juego?"
							message={`Seguro? Registrars tus puntos actuales (${score}) y perders las vidas restantes.`}
							brandColor={brandColor}
							customContent={
								<div className="game-quit-buttons">
									<button
										onClick={cancelQuit}
										className="game-quit-btn game-quit-btn-cancel"
									>
										 Cancelar
									</button>
									<button
										onClick={handleQuitConfirm}
										className="game-quit-btn game-quit-btn-confirm"
									>
										 Salir Ahora
									</button>
								</div>
							}
						/>
					)}
				</div>
			</div>
		</div>
	)
}

export default FlappyBirdGame
