import * as Tone from 'tone'

export class SnakeAudioManager {
	private initialized = false
	private muted = false

	// Timing protection for monophonic synths
	private lastSynthTime = 0
	private lastNoiseTime = 0
	private readonly MIN_TIME_GAP = 0.015

	// Synthesizers
	private popSynth: Tone.Synth | null = null
	private bellSynth: Tone.PolySynth | null = null
	private bassSynth: Tone.MembraneSynth | null = null
	private noiseSynth: Tone.NoiseSynth | null = null
	private melodySynth: Tone.PolySynth | null = null

	async init(): Promise<void> {
		if (this.initialized) return

		await Tone.start()

		// Pop synth - for eating food
		this.popSynth = new Tone.Synth({
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.1,
				sustain: 0,
				release: 0.1,
			},
			volume: -8,
		}).toDestination()

		// Bell synth - sparkly sounds for boost
		this.bellSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.2,
				sustain: 0.1,
				release: 0.4,
			},
			volume: -10,
		}).toDestination()

		// Bass synth - for game over thud
		this.bassSynth = new Tone.MembraneSynth({
			pitchDecay: 0.05,
			octaves: 4,
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.4,
				sustain: 0,
				release: 0.4,
			},
			volume: -6,
		}).toDestination()

		// Noise synth - for game over texture
		this.noiseSynth = new Tone.NoiseSynth({
			noise: { type: 'pink' },
			envelope: {
				attack: 0.005,
				decay: 0.15,
				sustain: 0,
				release: 0.1,
			},
			volume: -18,
		}).toDestination()

		// Melody synth - for speed up fanfare
		this.melodySynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.15,
				sustain: 0.2,
				release: 0.3,
			},
			volume: -10,
		}).toDestination()

		this.initialized = true
	}

	private getSafeTime(lastTime: number, synthKey: 'synth' | 'noise'): number {
		const now = Tone.now()
		const minNextTime = lastTime + this.MIN_TIME_GAP
		const safeTime = Math.max(now, minNextTime)

		if (synthKey === 'synth') this.lastSynthTime = safeTime
		else if (synthKey === 'noise') this.lastNoiseTime = safeTime

		return safeTime
	}

	// Pop sound when eating regular food
	playEatFood(): void {
		if (!this.initialized || this.muted || !this.popSynth) return
		const time = this.getSafeTime(this.lastSynthTime, 'synth')
		this.popSynth.triggerAttackRelease('C5', '16n', time)
	}

	// Sparkly arpeggio when eating boost item
	playEatBoost(): void {
		if (!this.initialized || this.muted || !this.bellSynth) return
		const now = Tone.now()
		const notes = ['E5', 'G5', 'B5', 'E6']
		notes.forEach((note, i) => {
			this.bellSynth!.triggerAttackRelease(note, '16n', now + i * 0.05)
		})
	}

	// Whoosh down when eating diet item
	playEatDiet(): void {
		if (!this.initialized || this.muted || !this.popSynth) return
		const time = this.getSafeTime(this.lastSynthTime, 'synth')
		this.popSynth.triggerAttackRelease('G4', '16n', time)
		this.popSynth.triggerAttackRelease('D4', '16n', time + 0.05)
		this.lastSynthTime = time + 0.05
	}

	// Bass thud + noise for game over
	playGameOver(): void {
		if (!this.initialized || this.muted) return

		const bassTime = this.getSafeTime(this.lastSynthTime, 'synth')
		const noiseTime = this.getSafeTime(this.lastNoiseTime, 'noise')

		if (this.bassSynth) {
			this.bassSynth.triggerAttackRelease('C2', '4n', bassTime)
		}
		if (this.noiseSynth) {
			this.noiseSynth.triggerAttackRelease('8n', noiseTime)
		}
		if (this.melodySynth) {
			// Sad descending phrase
			const phrase = ['E4', 'D4', 'C4', 'B3']
			phrase.forEach((note, i) => {
				this.melodySynth!.triggerAttackRelease(
					note,
					'8n',
					bassTime + 0.2 + i * 0.2
				)
			})
		}
	}

	// Ascending tone when speed increases
	playSpeedUp(): void {
		if (!this.initialized || this.muted || !this.melodySynth) return
		const now = Tone.now()
		const notes = ['C5', 'E5', 'G5']
		notes.forEach((note, i) => {
			this.melodySynth!.triggerAttackRelease(note, '32n', now + i * 0.06)
		})
	}

	setMuted(muted: boolean): void {
		this.muted = muted
	}

	isMuted(): boolean {
		return this.muted
	}

	dispose(): void {
		if (!this.initialized) return

		this.popSynth?.dispose()
		this.bellSynth?.dispose()
		this.bassSynth?.dispose()
		this.noiseSynth?.dispose()
		this.melodySynth?.dispose()

		this.popSynth = null
		this.bellSynth = null
		this.bassSynth = null
		this.noiseSynth = null
		this.melodySynth = null

		this.initialized = false
	}
}

export const snakeAudio = new SnakeAudioManager()
