import React, { forwardRef } from 'react'

interface GameCanvasProps {
	width: number
	height: number
	className?: string
	onClick?: () => void
	style?: React.CSSProperties
}

export const GameCanvas = forwardRef<HTMLCanvasElement, GameCanvasProps>(
	({ width, height, className = '', onClick, style }, ref) => {
		return (
			<div className="game-canvas-wrapper">
				{/* Decorative corner accents */}
				<div className="game-canvas-accent game-canvas-accent-tl" />
				<div className="game-canvas-accent game-canvas-accent-tr" />
				<div className="game-canvas-accent game-canvas-accent-bl" />
				<div className="game-canvas-accent game-canvas-accent-br" />

				{/* Canvas */}
				<canvas
					ref={ref}
					width={width}
					height={height}
					onClick={onClick}
					className={`game-canvas ${className}`}
					style={{
						imageRendering: 'pixelated',
						...style,
					}}
				/>
			</div>
		)
	}
)

GameCanvas.displayName = 'GameCanvas'
