import { useEffect, useState } from 'react'

interface ImageAssets {
	headUp: HTMLImageElement
	headDown: HTMLImageElement
	headLeft: HTMLImageElement
	headRight: HTMLImageElement
	tail: HTMLImageElement
	apple: HTMLImageElement
	banana: HTMLImageElement
	cherries: HTMLImageElement
	grapes: HTMLImageElement
	boost: HTMLImageElement
	diet: HTMLImageElement
	background: HTMLImageElement
}

export const useImageLoader = (
	skin?: string
): [ImageAssets | null, boolean, string | null] => {
	const [images, setImages] = useState<ImageAssets | null>(null)
	const [loading, setLoading] = useState(true)
	const [error, setError] = useState<string | null>(null)

	useEffect(() => {
		const loadImage = (src: string): Promise<HTMLImageElement> => {
			return new Promise((resolve, reject) => {
				const img = new Image()
				img.onload = () => resolve(img)
				img.onerror = () => reject(new Error(`Failed to load image: ${src}`))
				img.src = src
			})
		}

		const loadAllImages = async () => {
			try {
				setLoading(true)
				setError(null)

				const basePath = `/games/space-snake/img/`
				const imagePath = skin ? `skins/${skin}/` : ''

				const [
					headUp,
					headDown,
					headLeft,
					headRight,
					tail,
					apple,
					banana,
					cherries,
					grapes,
					boost,
					diet,
					background,
				] = await Promise.all([
					loadImage(`${basePath}${imagePath}head-up.webp`),
					loadImage(`${basePath}${imagePath}head-down.webp`),
					loadImage(`${basePath}${imagePath}head-left.webp`),
					loadImage(`${basePath}${imagePath}head-right.webp`),
					loadImage(`${basePath}${imagePath}tail.webp`),
					loadImage(`${basePath}${imagePath}apple.webp`),
					loadImage(`${basePath}${imagePath}banana.webp`),
					loadImage(`${basePath}${imagePath}cherries.webp`),
					loadImage(`${basePath}${imagePath}grapes.webp`),
					loadImage(`${basePath}${imagePath}boost.webp`).catch(() =>
						loadImage(`${basePath}${imagePath}grapes.webp`)
					), // Fallback to grapes if boost image not found
					loadImage(`${basePath}${imagePath}diet.webp`).catch(() =>
						loadImage(`${basePath}${imagePath}apple.webp`)
					), // Fallback to apple if diet image not found
					loadImage(`${basePath}${imagePath}bg.webp`), // Background from skin folder if skin is provided
				])

				setImages({
					headUp,
					headDown,
					headLeft,
					headRight,
					tail,
					apple,
					banana,
					cherries,
					grapes,
					boost,
					diet,
					background,
				})
			} catch (err) {
				setError(err instanceof Error ? err.message : 'Failed to load images')
			} finally {
				setLoading(false)
			}
		}

		loadAllImages()
	}, [skin])

	return [images, loading, error]
}
