import * as Tone from 'tone'

export class TetrisAudioManager {
	private initialized = false
	private muted = false

	// Timing protection
	private lastMoveTime = 0
	private readonly MIN_MOVE_GAP = 0.04

	// Synthesizers
	private moveSynth: Tone.Synth | null = null
	private rotateSynth: Tone.Synth | null = null
	private lockSynth: Tone.MembraneSynth | null = null
	private lineSynth: Tone.PolySynth | null = null
	private tetrisSynth: Tone.PolySynth | null = null
	private gameOverSynth: Tone.Synth | null = null

	async init(): Promise<void> {
		if (this.initialized) return

		await Tone.start()

		// Move synth - subtle click
		this.moveSynth = new Tone.Synth({
			oscillator: { type: 'square' },
			envelope: {
				attack: 0.001,
				decay: 0.03,
				sustain: 0,
				release: 0.02,
			},
			volume: -18,
		}).toDestination()

		// Rotate synth - whoosh
		this.rotateSynth = new Tone.Synth({
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.08,
				sustain: 0,
				release: 0.05,
			},
			volume: -12,
		}).toDestination()

		// Lock synth - thud when piece locks
		this.lockSynth = new Tone.MembraneSynth({
			pitchDecay: 0.02,
			octaves: 2,
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.15,
				sustain: 0,
				release: 0.1,
			},
			volume: -10,
		}).toDestination()

		// Line clear synth - escalating chime
		this.lineSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.15,
				sustain: 0.1,
				release: 0.3,
			},
			volume: -8,
		}).toDestination()

		// Tetris synth - special 4-line fanfare
		this.tetrisSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'sawtooth' },
			envelope: {
				attack: 0.01,
				decay: 0.2,
				sustain: 0.2,
				release: 0.5,
			},
			volume: -6,
		}).toDestination()

		// Game over synth - descending
		this.gameOverSynth = new Tone.Synth({
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.3,
				sustain: 0,
				release: 0.3,
			},
			volume: -8,
		}).toDestination()

		this.initialized = true
	}

	// Subtle click when moving piece
	playMove(): void {
		if (!this.initialized || this.muted || !this.moveSynth) return

		const now = Tone.now()
		if (now - this.lastMoveTime < this.MIN_MOVE_GAP) return

		this.lastMoveTime = now
		this.moveSynth.triggerAttackRelease('G5', '64n', now)
	}

	// Whoosh when rotating
	playRotate(): void {
		if (!this.initialized || this.muted || !this.rotateSynth) return
		const now = Tone.now()
		this.rotateSynth.triggerAttackRelease('C5', '32n', now)
		this.rotateSynth.triggerAttackRelease('E5', '32n', now + 0.02)
	}

	// Thud when piece locks in place
	playLock(): void {
		if (!this.initialized || this.muted || !this.lockSynth) return
		const now = Tone.now()
		this.lockSynth.triggerAttackRelease('C3', '8n', now)
	}

	// Escalating chime for line clears (1-3 lines)
	playLineClear(count: number): void {
		if (!this.initialized || this.muted || !this.lineSynth) return

		// If it's a Tetris (4 lines), use the special sound
		if (count >= 4) {
			this.playTetris()
			return
		}

		const now = Tone.now()
		// Escalating notes based on count
		const noteSequences: string[][] = [['C5'], ['C5', 'E5'], ['C5', 'E5', 'G5']]

		const notes = noteSequences[Math.min(count - 1, 2)]
		notes.forEach((note, i) => {
			this.lineSynth!.triggerAttackRelease(note, '8n', now + i * 0.08)
		})
	}

	// Special 4-line Tetris fanfare
	playTetris(): void {
		if (!this.initialized || this.muted || !this.tetrisSynth) return
		const now = Tone.now()

		// Triumphant fanfare
		const fanfare = [
			{ notes: ['C4', 'E4', 'G4'], time: 0 },
			{ notes: ['E4', 'G4', 'B4'], time: 0.1 },
			{ notes: ['G4', 'B4', 'D5'], time: 0.2 },
			{ notes: ['C5', 'E5', 'G5', 'C6'], time: 0.35 },
		]

		fanfare.forEach(({ notes, time }) => {
			this.tetrisSynth!.triggerAttackRelease(notes, '4n', now + time)
		})
	}

	// Sad descending phrase for game over
	playGameOver(): void {
		if (!this.initialized || this.muted || !this.gameOverSynth) return
		const now = Tone.now()

		const notes = ['E4', 'D4', 'C4', 'B3', 'A3', 'G3']
		notes.forEach((note, i) => {
			this.gameOverSynth!.triggerAttackRelease(note, '8n', now + i * 0.2)
		})
	}

	setMuted(muted: boolean): void {
		this.muted = muted
	}

	isMuted(): boolean {
		return this.muted
	}

	dispose(): void {
		if (!this.initialized) return

		this.moveSynth?.dispose()
		this.rotateSynth?.dispose()
		this.lockSynth?.dispose()
		this.lineSynth?.dispose()
		this.tetrisSynth?.dispose()
		this.gameOverSynth?.dispose()

		this.moveSynth = null
		this.rotateSynth = null
		this.lockSynth = null
		this.lineSynth = null
		this.tetrisSynth = null
		this.gameOverSynth = null

		this.initialized = false
	}
}

export const tetrisAudio = new TetrisAudioManager()
