import React, { useRef, useEffect, useCallback, useState } from 'react'
import { GameCanvas } from '../ui/GameCanvas'
import { Position, Direction, Food, FOOD_TYPES, SnakeGameProps } from './types'
import { snakeAudio } from './utils/audioManager'

const SnakeGame: React.FC<SnakeGameProps> = ({
	images,
	onScoreUpdate,
	onGameOver,
	isPaused,
	boardSize = 20,
	cellSize = 20,
}) => {
	const canvasRef = useRef<HTMLCanvasElement>(null)
	const [snake, setSnake] = useState<Position[]>([{ x: 10, y: 10 }])
	const [food, setFood] = useState<Food>({
		position: { x: 15, y: 15 },
		points: 1,
		type: 'apple',
	})
	const [direction, setDirection] = useState<Direction>('RIGHT')
	const [nextDirection, setNextDirection] = useState<Direction>('RIGHT')
	const [speed, setSpeed] = useState<number>(200)
	const lastUpdateTime = useRef<number>(0)
	const animationFrameId = useRef<number>(0)
	const currentScore = useRef<number>(0)
	const [boostActive, setBoostActive] = useState<boolean>(false)
	const [boostTimer, setBoostTimer] = useState<number>(0)
	const boostMultiplier = useRef<number>(1)
	const hasCollided = useRef<boolean>(false)

	const canvasSize = boardSize * cellSize

	// Initialize audio and reset collision flag when component mounts
	useEffect(() => {
		hasCollided.current = false
		snakeAudio.init()
		return () => {
			hasCollided.current = false
			snakeAudio.dispose()
		}
	}, [])

	// Get random position that doesn't overlap with snake
	const getRandomPosition = useCallback((): Position => {
		const generatePosition = (): Position => ({
			x: Math.floor(Math.random() * boardSize),
			y: Math.floor(Math.random() * boardSize),
		})

		let pos = generatePosition()
		// eslint-disable-next-line no-loop-func
		while (snake.some(segment => segment.x === pos.x && segment.y === pos.y)) {
			pos = generatePosition()
		}
		return pos
	}, [boardSize, snake])

	// Get random food item based on rarity
	const getRandomFood = useCallback((): Food => {
		// Calculate total rarity weight
		const totalWeight = FOOD_TYPES.reduce(
			(sum, food) => sum + (food.rarity || 0),
			0
		)
		let random = Math.random() * totalWeight

		// Select food based on weighted random
		let selectedFood = FOOD_TYPES[0]
		for (const foodType of FOOD_TYPES) {
			random -= foodType.rarity || 0
			if (random <= 0) {
				selectedFood = foodType
				break
			}
		}

		return {
			position: getRandomPosition(),
			points: selectedFood.points,
			type: selectedFood.type,
		}
	}, [getRandomPosition])

	// Handle keyboard input
	const handleKeyDown = useCallback(
		(event: KeyboardEvent) => {
			if (isPaused) return

			// Prevent default for arrow keys
			if (event.key.startsWith('Arrow')) {
				event.preventDefault()
			}

			// Queue direction changes to prevent immediate reversal
			switch (event.key) {
				case 'ArrowUp':
					if (direction !== 'DOWN') {
						setNextDirection('UP')
					}
					break
				case 'ArrowDown':
					if (direction !== 'UP') {
						setNextDirection('DOWN')
					}
					break
				case 'ArrowLeft':
					if (direction !== 'RIGHT') {
						setNextDirection('LEFT')
					}
					break
				case 'ArrowRight':
					if (direction !== 'LEFT') {
						setNextDirection('RIGHT')
					}
					break
			}
		},
		[isPaused, direction]
	)

	// Setup keyboard event listeners
	useEffect(() => {
		window.addEventListener('keydown', handleKeyDown)
		return () => window.removeEventListener('keydown', handleKeyDown)
	}, [handleKeyDown])

	// Move snake
	const moveSnake = useCallback(() => {
		if (isPaused) return

		// Update direction to queued direction
		setDirection(nextDirection)

		setSnake(currentSnake => {
			const newSnake = [...currentSnake]
			const head = { ...newSnake[0] }

			// Move head based on direction
			switch (nextDirection) {
				case 'UP':
					head.y -= 1
					break
				case 'DOWN':
					head.y += 1
					break
				case 'LEFT':
					head.x -= 1
					break
				case 'RIGHT':
					head.x += 1
					break
			}

			// Wrap around board edges
			if (head.x < 0) head.x = boardSize - 1
			if (head.x >= boardSize) head.x = 0
			if (head.y < 0) head.y = boardSize - 1
			if (head.y >= boardSize) head.y = 0

			// Check self collision
			if (
				newSnake.some(segment => segment.x === head.x && segment.y === head.y)
			) {
				// Collision detected - notify parent only once
				if (!hasCollided.current) {
					hasCollided.current = true
					snakeAudio.playGameOver()
					// Use setTimeout to avoid setState during render
					setTimeout(() => {
						onGameOver()
					}, 0)
				}
				return currentSnake
			}

			// Add new head
			newSnake.unshift(head)

			// Check food collision
			if (head.x === food.position.x && head.y === food.position.y) {
				// Handle special effects
				const foodType = FOOD_TYPES.find(f => f.type === food.type)
				if (foodType?.specialEffect === 'boost') {
					// Activate boost
					setBoostActive(true)
					setBoostTimer(10000) // 10 seconds
					boostMultiplier.current = 2
					snakeAudio.playEatBoost()
				} else if (foodType?.specialEffect === 'diet' && newSnake.length > 1) {
					// Reduce snake size by 3 segments (minimum 1 segment)
					const segmentsToRemove = Math.min(3, newSnake.length - 1)
					for (let i = 0; i < segmentsToRemove; i++) {
						newSnake.pop()
					}
					snakeAudio.playEatDiet()
				} else {
					snakeAudio.playEatFood()
				}

				// Eat food and calculate score with boost multiplier
				const baseScore = food.points
				const actualScore = baseScore * boostMultiplier.current
				currentScore.current += actualScore
				// Use setTimeout to avoid setState during render
				setTimeout(() => {
					onScoreUpdate(actualScore)
				}, 0)
				setFood(getRandomFood())

				// Increase speed every 25 points with more progressive increments
				if (currentScore.current % 25 === 0) {
					setSpeed(prev => Math.max(40, prev - 10))
					snakeAudio.playSpeedUp()
				}
			} else {
				// Remove tail if no food eaten
				newSnake.pop()
			}

			return newSnake
		})
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [isPaused, nextDirection, boardSize, food, getRandomFood, onGameOver])

	// Update boost timer
	useEffect(() => {
		if (boostActive && boostTimer > 0) {
			const interval = setInterval(() => {
				setBoostTimer(prev => {
					if (prev <= 100) {
						setBoostActive(false)
						boostMultiplier.current = 1
						return 0
					}
					return prev - 100
				})
			}, 100)
			return () => clearInterval(interval)
		}
	}, [boostActive, boostTimer])

	// Game loop
	useEffect(() => {
		const gameLoop = (timestamp: number) => {
			if (timestamp - lastUpdateTime.current >= speed) {
				moveSnake()
				lastUpdateTime.current = timestamp
			}
			animationFrameId.current = requestAnimationFrame(gameLoop)
		}

		animationFrameId.current = requestAnimationFrame(gameLoop)
		return () => {
			if (animationFrameId.current) {
				cancelAnimationFrame(animationFrameId.current)
			}
		}
	}, [moveSnake, speed])

	// Drawing
	useEffect(() => {
		const canvas = canvasRef.current
		if (!canvas) return

		const ctx = canvas.getContext('2d')
		if (!ctx) return

		// Clear canvas
		ctx.clearRect(0, 0, canvasSize, canvasSize)

		// Draw background
		ctx.drawImage(images.background, 0, 0, canvasSize, canvasSize)

		// Draw grid overlay (subtle)
		ctx.strokeStyle = 'rgba(255, 255, 255, 0.1)'
		ctx.lineWidth = 0.5
		for (let i = 0; i <= boardSize; i++) {
			ctx.beginPath()
			ctx.moveTo(i * cellSize, 0)
			ctx.lineTo(i * cellSize, canvasSize)
			ctx.moveTo(0, i * cellSize)
			ctx.lineTo(canvasSize, i * cellSize)
			ctx.stroke()
		}

		// Draw snake
		snake.forEach((segment, index) => {
			if (index === 0) {
				// Draw head based on direction
				let headImage: HTMLImageElement
				switch (direction) {
					case 'UP':
						headImage = images.headUp
						break
					case 'DOWN':
						headImage = images.headDown
						break
					case 'LEFT':
						headImage = images.headLeft
						break
					case 'RIGHT':
						headImage = images.headRight
						break
				}
				ctx.drawImage(
					headImage,
					segment.x * cellSize,
					segment.y * cellSize,
					cellSize,
					cellSize
				)
			} else {
				// Draw tail for body segments
				ctx.drawImage(
					images.tail,
					segment.x * cellSize,
					segment.y * cellSize,
					cellSize,
					cellSize
				)
			}
		})

		// Draw food
		let foodImage: HTMLImageElement
		switch (food.type) {
			case 'apple':
				foodImage = images.apple
				break
			case 'banana':
				foodImage = images.banana
				break
			case 'cherries':
				foodImage = images.cherries
				break
			case 'grapes':
				foodImage = images.grapes
				break
			case 'boost':
				foodImage = images.boost
				break
			case 'diet':
				foodImage = images.diet
				break
		}
		ctx.drawImage(
			foodImage,
			food.position.x * cellSize,
			food.position.y * cellSize,
			cellSize,
			cellSize
		)

		// Draw boost indicator
		if (boostActive) {
			// Golden border effect
			ctx.strokeStyle = '#FFD700'
			ctx.lineWidth = 4
			ctx.strokeRect(2, 2, canvasSize - 4, canvasSize - 4)

			// Boost timer text
			ctx.fillStyle = '#FFD700'
			ctx.font = 'bold 16px Arial'
			ctx.shadowColor = 'black'
			ctx.shadowBlur = 2
			ctx.fillText(`Boost: ${(boostTimer / 1000).toFixed(1)}s`, 10, 25)
			ctx.shadowBlur = 0
		}
	}, [
		snake,
		food,
		direction,
		images,
		boardSize,
		cellSize,
		canvasSize,
		boostActive,
		boostTimer,
	])

	return (
		<GameCanvas
			ref={canvasRef}
			width={canvasSize}
			height={canvasSize}
			className="border-4 border-green-500/50 rounded-lg shadow-2xl"
			style={{
				imageRendering: 'auto',
			}}
		/>
	)
}

export default SnakeGame
