import React from 'react'

interface GameCardProps {
	title: string
	icon?: string
	color?: string
	gradientFrom?: string
	gradientTo?: string
	onClick: () => void
	description?: string
	disabled?: boolean
}

export const GameCard: React.FC<GameCardProps> = ({
	title,
	icon,
	color,
	gradientFrom,
	gradientTo,
	onClick,
	description,
	disabled = false,
}) => {
	const bgStyle =
		gradientFrom && gradientTo
			? {
					background: `linear-gradient(135deg, ${gradientFrom} 0%, ${gradientTo} 100%)`,
				}
			: { backgroundColor: color || '#4b5563' }

	return (
		<button
			onClick={onClick}
			disabled={disabled}
			className="game-card"
			style={bgStyle}
		>
			{/* Hover Overlay */}
			<div className="game-card-overlay" />

			{/* Content */}
			<div className="game-card-content">
				{/* Icon */}
				{icon && <div className="game-card-icon">{icon}</div>}

				{/* Title */}
				<h3 className="game-card-title">{title}</h3>

				{/* Description */}
				{description && (
					<p className="game-card-description">{description}</p>
				)}
			</div>

			{/* Corner Accent */}
			<div className="game-card-accent-top">
				<div className="game-card-accent-triangle" />
			</div>

			{/* Bottom Accent */}
			<div className="game-card-accent-bottom" />
		</button>
	)
}
