import { useEffect, useState } from 'react'

interface ImageAssets {
	bird: HTMLImageElement
	background: HTMLImageElement
	foreground: HTMLImageElement
	pipeNorth: HTMLImageElement
	pipeSouth: HTMLImageElement
	coin: HTMLImageElement
	heart: HTMLImageElement
}

export const useImageLoader = (
	skin?: string
): [ImageAssets | null, boolean, string | null] => {
	const [images, setImages] = useState<ImageAssets | null>(null)
	const [loading, setLoading] = useState(true)
	const [error, setError] = useState<string | null>(null)

	useEffect(() => {
		const loadImage = (src: string): Promise<HTMLImageElement> => {
			return new Promise((resolve, reject) => {
				const img = new Image()
				img.onload = () => resolve(img)
				img.onerror = () => reject(new Error(`Failed to load image: ${src}`))
				img.src = src
			})
		}

		const loadAllImages = async () => {
			try {
				setLoading(true)
				setError(null)

				const basePath = `/games/flappy-bird/`
				const skinPath = skin ? `skins/${skin}/` : ''

				const [
					bird,
					background,
					foreground,
					pipeNorth,
					pipeSouth,
					coin,
					heart,
				] = await Promise.all([
					loadImage(`${basePath}${skinPath}bird.webp`),
					loadImage(`${basePath}${skinPath}bg.webp`),
					loadImage(`${basePath}${skinPath}fg.webp`),
					loadImage(`${basePath}${skinPath}pipeNorth.webp`),
					loadImage(`${basePath}${skinPath}pipeSouth.webp`),
					loadImage(`${basePath}${skinPath}coin.webp`),
					loadImage(`${basePath}${skinPath}heart.webp`),
				])

				setImages({
					bird,
					background,
					foreground,
					pipeNorth,
					pipeSouth,
					coin,
					heart,
				})
			} catch (err) {
				setError(err instanceof Error ? err.message : 'Failed to load images')
			} finally {
				setLoading(false)
			}
		}

		loadAllImages()
	}, [skin])

	return [images, loading, error]
}
