import React, { useState, useEffect, useRef } from 'react';
import { Language } from '../../../types/i18n';
import { getTranslations } from '../../../i18n';

interface NameChangeModalProps {
  isOpen: boolean;
  currentName: string;
  onSave: (name: string) => void;
  onClose: () => void;
  lang: Language;
}

const NameChangeModal: React.FC<NameChangeModalProps> = ({
  isOpen,
  currentName,
  onSave,
  onClose,
  lang,
}) => {
  const [name, setName] = useState(currentName);
  const inputRef = useRef<HTMLInputElement>(null);
  const t = getTranslations(lang);

  useEffect(() => {
    if (isOpen) {
      setName(currentName);
      setTimeout(() => inputRef.current?.focus(), 100);
    }
  }, [isOpen, currentName]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSave(name);
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="name-modal-overlay" onClick={onClose}>
      <div className="name-modal" onClick={(e) => e.stopPropagation()}>
        <div className="name-modal-titlebar">
          <span>{t.browser.settings.changeName}</span>
          <button className="name-modal-close" onClick={onClose}>
            
          </button>
        </div>
        <form onSubmit={handleSubmit} className="name-modal-content">
          <label htmlFor="name-input">{t.welcome.nameLabel}</label>
          <input
            ref={inputRef}
            type="text"
            id="name-input"
            value={name}
            onChange={(e) => setName(e.target.value)}
            placeholder={t.welcome.namePlaceholder}
          />
          <div className="name-modal-buttons">
            <button type="submit" className="name-modal-ok">
              OK
            </button>
            <button type="button" className="name-modal-cancel" onClick={onClose}>
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default NameChangeModal;
