'use client'

import React, { useRef, useEffect, useState, useCallback } from 'react'
import { GameHeader } from '../ui/GameHeader'
import { GameCanvas } from '../ui/GameCanvas'
import { GameOverlay } from '../ui/GameOverlay'
import { isMobile } from '../../../../utils/functions'
import type { RhythmHighwayProps, Note, HitEffect } from './types'
import {
	CANVAS_WIDTH,
	CANVAS_HEIGHT,
	LANE_COUNT,
	LANE_WIDTH,
	HIT_ZONE_Y,
	HIT_ZONE_HEIGHT,
	NOTE_HEIGHT,
	NOTE_RADIUS,
	PERFECT_WINDOW,
	GOOD_WINDOW,
	PERFECT_POINTS,
	GOOD_POINTS,
	MAX_COMBO_MULTIPLIER,
	DEFAULT_LANE_COLORS,
	LANE_KEYS,
	LANE_LABELS,
	HIT_EFFECT_DURATION,
} from './constants'
import { audioManager } from './utils/audioManager'
import { getSongForLevel, type Song, type Difficulty } from './songs'
import type { DrumPattern } from './songs/songTypes'

// Use a web-safe font that looks similar to Orbitron
const GAME_FONT_FAMILY = "'Orbitron', 'Segoe UI', 'Arial Black', sans-serif"

// Speed by difficulty (pixels per frame at 60fps)
const SPEED_BY_DIFFICULTY: Record<string, number> = {
	'very-easy': 2,
	easy: 3,
	medium: 5,
	hard: 7,
	'very-hard': 9,
}

// Difficulty display names in Spanish
const DIFFICULTY_NAMES: Record<string, string> = {
	'very-easy': 'Muy Fcil',
	easy: 'Fcil',
	medium: 'Medio',
	hard: 'Difcil',
	'very-hard': 'Muy Difcil',
}

const RhythmHighway: React.FC<RhythmHighwayProps> = ({
	setGameOver,
	setPointsEarned,
	brandColor = '#8b5cf6',
	initialLives = 3,
	gameTitle = 'RHYTHM HIGHWAY',
	brandLogo,
}) => {
	const mobile = isMobile()
	const canvasRef = useRef<HTMLCanvasElement>(null)
	const animationFrameId = useRef<number | null>(null)

	// Game state
	const [gameStarted, setGameStarted] = useState(false)
	const [gamePaused, setGamePaused] = useState(false)
	const [quitGame, setQuitGame] = useState(false)
	const [wasGamePaused, setWasGamePaused] = useState(false)
	const [instructionsStep, setInstructionsStep] = useState(0)

	// Level progression
	const [level, setLevel] = useState(1)
	const [showLevelUp, setShowLevelUp] = useState(false)
	const [levelUpTimeout, setLevelUpTimeout] = useState<NodeJS.Timeout | null>(
		null
	)

	// Score and progress
	const [points, setPoints] = useState(0)
	const [currentLives, setCurrentLives] = useState(initialLives)
	const [combo, setCombo] = useState(0)
	const [maxCombo, setMaxCombo] = useState(0)

	// Audio state
	const [audioInitialized, setAudioInitialized] = useState(false)
	const [audioMuted, setAudioMuted] = useState(false)

	// Game refs (to avoid closure issues in animation loop)
	const notesRef = useRef<Note[]>([])
	const hitEffectsRef = useRef<HitEffect[]>([])
	const pointsRef = useRef(0)
	const comboRef = useRef(0)
	const livesRef = useRef(initialLives)
	const lastBeatRef = useRef(-1)
	const gameTimeRef = useRef(0)
	const noteIdCounterRef = useRef(0)
	const levelRef = useRef(1)
	const currentSongRef = useRef<Song | null>(null)
	const songNoteIndexRef = useRef(0) // Track which song note to spawn next
	const songLoopCountRef = useRef(0) // Track song loops for level progression
	const currentConfigRef = useRef<{
		song: Song
		difficulty: Difficulty
		track: DrumPattern
		speedMultiplier: number
		cycle: number
		displayName: string
	} | null>(null) // Cache current level config to prevent random song changes per frame
	const nextLevelConfigRef = useRef<{
		song: Song
		difficulty: Difficulty
		track: DrumPattern
		speedMultiplier: number
		cycle: number
		displayName: string
	} | null>(null) // Cache next level config for level-up overlay to ensure announced song matches played song

	// Get current level config with song data
	const getLevelConfig = useCallback((lvl: number) => {
		const { song, cycle, speedMultiplier } = getSongForLevel(lvl)
		const difficulty = song.metadata.difficulty
		const difficultyName = DIFFICULTY_NAMES[difficulty] || difficulty

		return {
			song,
			difficulty,
			track: song.background.drumPattern,
			speedMultiplier,
			cycle,
			displayName:
				cycle > 0
					? `${song.metadata.title} (${difficultyName}) x${cycle + 1}`
					: `${song.metadata.title} (${difficultyName})`,
		}
	}, [])

	// Current level configuration (for UI display)
	const currentConfig = getLevelConfig(level)

	// Lane colors
	const laneColors = DEFAULT_LANE_COLORS

	// Sync refs with state
	useEffect(() => {
		pointsRef.current = points
	}, [points])

	useEffect(() => {
		comboRef.current = combo
	}, [combo])

	useEffect(() => {
		livesRef.current = currentLives
	}, [currentLives])

	useEffect(() => {
		levelRef.current = level
	}, [level])

	// Initialize audio
	useEffect(() => {
		const initAudio = async () => {
			await audioManager.init()
			setAudioInitialized(true)
		}
		initAudio()

		return () => {
			audioManager.dispose()
		}
	}, [])

	// Handle game over
	useEffect(() => {
		if (currentLives <= 0 && gameStarted) {
			audioManager.stopBackgroundBeat()
			setGameStarted(false)
			setGameOver(true)
			setPointsEarned(points)
		}
	}, [currentLives, gameStarted, points, setGameOver, setPointsEarned])

	// Handle pause/resume background beat
	useEffect(() => {
		if (!gameStarted) return
		if (gamePaused || quitGame) {
			audioManager.pauseBackgroundBeat()
		} else {
			audioManager.resumeBackgroundBeat()
		}
	}, [gamePaused, quitGame, gameStarted])

	// Cleanup level up timeout
	useEffect(() => {
		return () => {
			if (levelUpTimeout) {
				clearTimeout(levelUpTimeout)
			}
		}
	}, [levelUpTimeout])

	// Handle level up
	const triggerLevelUp = useCallback(() => {
		// Pre-cache the next level config so overlay and actual transition use the same song
		const nextConfig = getLevelConfig(levelRef.current + 1)
		nextLevelConfigRef.current = nextConfig

		setShowLevelUp(true)
		setGamePaused(true)

		// Play level up sound
		if (audioInitialized) {
			audioManager.playCombo()
		}

		// Auto-continue after showing level up message
		const timeout = setTimeout(() => {
			const newLevel = levelRef.current + 1
			setLevel(newLevel)
			setShowLevelUp(false)
			setGamePaused(false)
			songLoopCountRef.current = 0
			songNoteIndexRef.current = 0

			// Clear notes for clean transition
			notesRef.current = []
			lastBeatRef.current = -1
			gameTimeRef.current = 0

			// Use the pre-cached config instead of calling getLevelConfig again
			// This ensures the song announced in the overlay matches the song that plays
			const newConfig = nextLevelConfigRef.current!
			currentConfigRef.current = newConfig
			const newSong = newConfig.song
			currentSongRef.current = newSong
			const newBpm = newSong.metadata.bpm

			// Clear the next level config cache
			nextLevelConfigRef.current = null

			// Restart beat with new song's BPM and drum pattern
			audioManager.stopBackgroundBeat()
			if (newSong.background.drumPattern !== 'none') {
				audioManager.startBackgroundBeat(
					newBpm,
					newSong.background.drumPattern as 'chill' | 'default' | 'upbeat',
					newConfig.cycle
				)
			}
		}, 2000)

		setLevelUpTimeout(timeout)
	}, [audioInitialized, getLevelConfig])

	// Check for hit
	const checkHit = useCallback(
		(lane: number) => {
			if (!gameStarted || gamePaused) return

			const notes = notesRef.current
			const hitZoneCenter = HIT_ZONE_Y + HIT_ZONE_HEIGHT / 2

			// Find the closest unhit note in this lane
			let closestNote: Note | null = null
			let closestDistance = Infinity

			for (const note of notes) {
				if (note.lane === lane && !note.hit && !note.missed) {
					const noteCenter = note.y + NOTE_HEIGHT / 2
					const distance = Math.abs(noteCenter - hitZoneCenter)
					if (distance < closestDistance) {
						closestDistance = distance
						closestNote = note
					}
				}
			}

			if (closestNote) {
				const noteCenter = closestNote.y + NOTE_HEIGHT / 2
				const distance = Math.abs(noteCenter - hitZoneCenter)

				// Convert pixel distance to time based on note speed
				const currentSpeed =
					SPEED_BY_DIFFICULTY[getLevelConfig(levelRef.current).difficulty] *
					getLevelConfig(levelRef.current).speedMultiplier
				const timeDistance = (distance / currentSpeed) * (1000 / 60)

				if (timeDistance <= GOOD_WINDOW) {
					closestNote.hit = true
					closestNote.hitTime = Date.now()

					if (timeDistance <= PERFECT_WINDOW) {
						// Perfect hit
						closestNote.hitType = 'perfect'
						const multiplier = Math.min(
							comboRef.current + 1,
							MAX_COMBO_MULTIPLIER
						)
						const earnedPoints = PERFECT_POINTS * multiplier
						setPoints(prev => prev + earnedPoints)
						setCombo(prev => {
							const newCombo = prev + 1
							setMaxCombo(max => Math.max(max, newCombo))
							if (newCombo % 10 === 0 && audioInitialized) {
								audioManager.playCombo()
							}
							return newCombo
						})

						hitEffectsRef.current.push({
							id: Date.now(),
							lane,
							type: 'perfect',
							time: Date.now(),
						})

						if (audioInitialized) {
							// Play melodic note if available, otherwise fall back to lane pitch
							if (closestNote.pitch) {
								audioManager.playMelodicNotePerfect(closestNote.pitch)
							} else {
								audioManager.playPerfect(lane)
							}
						}
					} else {
						// Good hit
						closestNote.hitType = 'good'
						const multiplier = Math.min(
							comboRef.current + 1,
							MAX_COMBO_MULTIPLIER
						)
						const earnedPoints = GOOD_POINTS * multiplier
						setPoints(prev => prev + earnedPoints)
						setCombo(prev => {
							const newCombo = prev + 1
							setMaxCombo(max => Math.max(max, newCombo))
							return newCombo
						})

						hitEffectsRef.current.push({
							id: Date.now(),
							lane,
							type: 'good',
							time: Date.now(),
						})

						if (audioInitialized) {
							// Play melodic note if available, otherwise fall back to lane pitch
							if (closestNote.pitch) {
								audioManager.playMelodicNote(closestNote.pitch)
							} else {
								audioManager.playNote(lane)
							}
						}
					}
				}
			} else {
				// No note to hit - miss effect (but no penalty for pressing when no note)
				hitEffectsRef.current.push({
					id: Date.now(),
					lane,
					type: 'miss',
					time: Date.now(),
				})
			}
		},
		[gameStarted, gamePaused, audioInitialized, getLevelConfig]
	)

	// Keyboard controls
	useEffect(() => {
		const handleKeyDown = (e: KeyboardEvent) => {
			const key = e.key.toLowerCase()
			const laneIndex = LANE_KEYS.indexOf(key)

			// Prevent ALL default behaviors for game keys to avoid scrolling/jumping
			if (laneIndex !== -1 || key === 'p' || key === ' ' || key === 'escape') {
				e.preventDefault()
				e.stopPropagation()
			}

			if (!gameStarted) return

			if (laneIndex !== -1) {
				checkHit(laneIndex)
			} else if (key === 'p' || key === ' ') {
				if (!quitGame && !showLevelUp) {
					setGamePaused(prev => !prev)
				}
			}
		}

		document.addEventListener('keydown', handleKeyDown, { passive: false })
		return () => document.removeEventListener('keydown', handleKeyDown)
	}, [gameStarted, checkHit, quitGame, showLevelUp])

	// Touch controls
	const handleLaneTouch = useCallback(
		(lane: number) => {
			if (!gameStarted || gamePaused) return
			checkHit(lane)
		},
		[gameStarted, gamePaused, checkHit]
	)

	// Game loop
	useEffect(() => {
		const canvas = canvasRef.current
		if (!canvas || !gameStarted) return
		const ctx = canvas.getContext('2d')
		if (!ctx) return

		let lastTime = performance.now()

		const gameLoop = (currentTime: number) => {
			const deltaTime = currentTime - lastTime
			lastTime = currentTime

			if (gamePaused || quitGame) {
				animationFrameId.current = requestAnimationFrame(gameLoop)
				return
			}

			// Use cached config to avoid random song selection every frame
			// If no config cached yet (shouldn't happen), initialize it
			if (!currentConfigRef.current) {
				currentConfigRef.current = getLevelConfig(levelRef.current)
			}
			const config = currentConfigRef.current
			const song = config.song
			const currentBpm = song.metadata.bpm
			const currentBeatDuration = 60000 / currentBpm
			const currentNoteSpeed =
				SPEED_BY_DIFFICULTY[config.difficulty] * config.speedMultiplier

			// Update game time
			gameTimeRef.current += deltaTime

			// Calculate current beat position
			const currentBeat = gameTimeRef.current / currentBeatDuration
			const lastBeat = lastBeatRef.current

			// Determine loop boundaries from song
			const loopStart = song.loop?.startBeat ?? 0
			const loopEnd =
				song.loop?.endBeat ?? Math.max(...song.notes.map(n => n.beat)) + 4

			// Check if we've passed the loop end point (for level progression)
			let spawnBeat = currentBeat // Use this for note spawning (may be reset on loop)

			if (lastBeat > 0 && currentBeat >= loopEnd && lastBeat < loopEnd) {
				songLoopCountRef.current++
				const loopsNeeded = song.loop?.count || 1

				if (songLoopCountRef.current >= loopsNeeded) {
					// Trigger level up
					setTimeout(() => triggerLevelUp(), 100)
				} else {
					// Loop the song - reset to loop start
					gameTimeRef.current = loopStart * currentBeatDuration
					songNoteIndexRef.current = song.notes.findIndex(
						n => n.beat >= loopStart
					)
					if (songNoteIndexRef.current === -1) songNoteIndexRef.current = 0
					// Reset spawnBeat so we don't spawn all notes at once
					spawnBeat = loopStart
					lastBeatRef.current = loopStart - 0.1 // Prevent re-triggering
				}
			}

			// Spawn notes from song in order
			while (songNoteIndexRef.current < song.notes.length) {
				const noteData = song.notes[songNoteIndexRef.current]

				// Check if this note should spawn now
				if (noteData.beat <= spawnBeat) {
					notesRef.current.push({
						id: noteIdCounterRef.current++,
						lane: noteData.lane,
						y: -NOTE_HEIGHT,
						speed: currentNoteSpeed,
						hit: false,
						missed: false,
						pitch: noteData.pitch, // Store the pitch for melodic playback
					})
					songNoteIndexRef.current++
				} else {
					break // Wait for the right beat
				}
			}
			lastBeatRef.current = currentBeat

			// Update notes
			const now = Date.now()
			notesRef.current = notesRef.current.filter(note => {
				if (note.hit) {
					return now - (note.hitTime || 0) < 200
				}

				// Move note down with its own speed (important for level transitions)
				note.y += note.speed * (deltaTime / 16.67)

				// Check if note passed the hit zone without being hit
				if (
					note.y > HIT_ZONE_Y + HIT_ZONE_HEIGHT + NOTE_HEIGHT &&
					!note.missed
				) {
					note.missed = true
					setCombo(0)
					setCurrentLives(prev => Math.max(0, prev - 1))
					if (audioInitialized) {
						audioManager.playMiss()
					}
				}

				return note.y < CANVAS_HEIGHT + NOTE_HEIGHT
			})

			// Update hit effects
			hitEffectsRef.current = hitEffectsRef.current.filter(
				effect => now - effect.time < HIT_EFFECT_DURATION
			)

			// Draw
			draw(ctx, config, gameTimeRef.current, currentNoteSpeed, currentBpm)

			animationFrameId.current = requestAnimationFrame(gameLoop)
		}

		const draw = (
			ctx: CanvasRenderingContext2D,
			config: ReturnType<typeof getLevelConfig>,
			elapsedTime: number,
			noteSpeed: number,
			bpm: number
		) => {
			// Clear canvas
			ctx.clearRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT)

			// Perspective transformation helpers
			const vanishY = -100 // Vanishing point Y (above canvas)
			const vanishX = CANVAS_WIDTH / 2 // Center X
			const bottomY = CANVAS_HEIGHT

			// Returns X position adjusted for perspective based on Y
			const getPerspectiveX = (baseX: number, y: number): number => {
				const perspectiveFactor = (y - vanishY) / (bottomY - vanishY)
				return vanishX + (baseX - vanishX) * perspectiveFactor
			}

			// Get width at a given Y (for notes)
			const getPerspectiveWidth = (baseWidth: number, y: number): number => {
				const perspectiveFactor = (y - vanishY) / (bottomY - vanishY)
				return baseWidth * perspectiveFactor
			}

			// Draw background gradient - changes based on track
			const bgGradient = ctx.createLinearGradient(0, 0, 0, CANVAS_HEIGHT)
			if (config.track === 'chill') {
				bgGradient.addColorStop(0, '#0a1a1a')
				bgGradient.addColorStop(0.5, '#0a2e2e')
				bgGradient.addColorStop(1, '#0a1a1a')
			} else if (config.track === 'upbeat') {
				bgGradient.addColorStop(0, '#1a0a0a')
				bgGradient.addColorStop(0.5, '#2e0a1a')
				bgGradient.addColorStop(1, '#1a0a0a')
			} else {
				bgGradient.addColorStop(0, '#0a0a1a')
				bgGradient.addColorStop(0.5, '#1a0a2e')
				bgGradient.addColorStop(1, '#0a0a1a')
			}
			ctx.fillStyle = bgGradient
			ctx.fillRect(0, 0, CANVAS_WIDTH, CANVAS_HEIGHT)

			// Draw road edges with glow for depth
			ctx.save()
			ctx.strokeStyle = brandColor || '#8b5cf6'
			ctx.lineWidth = 3
			ctx.shadowColor = brandColor || '#8b5cf6'
			ctx.shadowBlur = 10

			// Left edge
			ctx.beginPath()
			ctx.moveTo(getPerspectiveX(0, 0), 0)
			ctx.lineTo(getPerspectiveX(0, CANVAS_HEIGHT), CANVAS_HEIGHT)
			ctx.stroke()

			// Right edge
			ctx.beginPath()
			ctx.moveTo(getPerspectiveX(CANVAS_WIDTH, 0), 0)
			ctx.lineTo(getPerspectiveX(CANVAS_WIDTH, CANVAS_HEIGHT), CANVAS_HEIGHT)
			ctx.stroke()
			ctx.restore()

			// Draw beat markers with perspective-correct spacing
			// Work in depth space (0 = vanishing point, 1 = bottom of screen)
			const beatInterval = 60000 / bpm
			const pixelsPerBeat = (noteSpeed * beatInterval) / 16.67 // How far a beat travels in pixels
			const depthPerBeat = pixelsPerBeat / (bottomY - vanishY) // Convert to depth space
			const depthSpeed = noteSpeed / (bottomY - vanishY) // Convert pixel speed to depth speed
			const depthOffset = ((elapsedTime * depthSpeed) / 16.67) % depthPerBeat

			ctx.strokeStyle = 'rgba(255, 255, 255, 0.12)'
			ctx.lineWidth = 1

			// Iterate through beats in depth space
			for (let depth = depthOffset; depth <= 1.0; depth += depthPerBeat) {
				// Convert depth to screen Y
				const y = vanishY + depth * (bottomY - vanishY)

				// Skip if above canvas
				if (y < 0) continue

				// Calculate perspective factor for dash scaling
				const perspectiveFactor = depth // depth IS the perspective factor (0 to 1)
				const dashSize = Math.max(2, 10 * perspectiveFactor)
				ctx.setLineDash([dashSize, dashSize])

				ctx.beginPath()
				ctx.moveTo(getPerspectiveX(0, y), y)
				ctx.lineTo(getPerspectiveX(CANVAS_WIDTH, y), y)
				ctx.stroke()
			}

			ctx.setLineDash([]) // Reset dash

			// Draw lane separators with perspective
			ctx.strokeStyle = 'rgba(255, 255, 255, 0.15)'
			ctx.lineWidth = 1
			for (let i = 0; i <= LANE_COUNT; i++) {
				const baseX = i * LANE_WIDTH
				ctx.beginPath()
				ctx.moveTo(getPerspectiveX(baseX, 0), 0)
				ctx.lineTo(getPerspectiveX(baseX, CANVAS_HEIGHT), CANVAS_HEIGHT)
				ctx.stroke()
			}

			// Draw hit zone
			const hitZoneGradient = ctx.createLinearGradient(
				0,
				HIT_ZONE_Y,
				0,
				HIT_ZONE_Y + HIT_ZONE_HEIGHT
			)
			hitZoneGradient.addColorStop(0, 'rgba(255, 255, 255, 0.1)')
			hitZoneGradient.addColorStop(0.5, 'rgba(255, 255, 255, 0.3)')
			hitZoneGradient.addColorStop(1, 'rgba(255, 255, 255, 0.1)')
			ctx.fillStyle = hitZoneGradient
			ctx.fillRect(0, HIT_ZONE_Y, CANVAS_WIDTH, HIT_ZONE_HEIGHT)

			// Draw hit zone line
			ctx.strokeStyle = 'rgba(255, 255, 255, 0.8)'
			ctx.lineWidth = 2
			ctx.beginPath()
			ctx.moveTo(0, HIT_ZONE_Y + HIT_ZONE_HEIGHT / 2)
			ctx.lineTo(CANVAS_WIDTH, HIT_ZONE_Y + HIT_ZONE_HEIGHT / 2)
			ctx.stroke()

			// Draw lane buttons at bottom
			const buttonY = CANVAS_HEIGHT - 60
			const buttonHeight = 50
			for (let i = 0; i < LANE_COUNT; i++) {
				const x = i * LANE_WIDTH
				const color = Object.values(laneColors)[i]

				ctx.fillStyle = `${color}33`
				ctx.fillRect(x + 5, buttonY, LANE_WIDTH - 10, buttonHeight)

				ctx.strokeStyle = color
				ctx.lineWidth = 2
				ctx.strokeRect(x + 5, buttonY, LANE_WIDTH - 10, buttonHeight)

				ctx.fillStyle = color
				ctx.font = `bold 20px ${GAME_FONT_FAMILY}`
				ctx.textAlign = 'center'
				ctx.textBaseline = 'middle'
				ctx.fillText(
					LANE_LABELS[i],
					x + LANE_WIDTH / 2,
					buttonY + buttonHeight / 2
				)
			}

			// Draw notes with perspective
			const now = Date.now()
			for (const note of notesRef.current) {
				const color = Object.values(laneColors)[note.lane]

				// Calculate perspective-adjusted position and size
				const laneBaseX = note.lane * LANE_WIDTH
				const perspXStart = getPerspectiveX(laneBaseX + 10, note.y)
				const perspXEnd = getPerspectiveX(laneBaseX + LANE_WIDTH - 10, note.y)
				const noteWidth = perspXEnd - perspXStart
				const noteHeight = getPerspectiveWidth(NOTE_HEIGHT, note.y)
				const perspRadius = Math.max(
					2,
					getPerspectiveWidth(NOTE_RADIUS, note.y)
				)

				if (note.hit) {
					const elapsed = now - (note.hitTime || 0)
					const progress = elapsed / 200
					const alpha = 1 - progress
					const scale = 1 + progress * 0.5

					ctx.save()
					ctx.globalAlpha = alpha
					ctx.translate(perspXStart + noteWidth / 2, note.y + noteHeight / 2)
					ctx.scale(scale, scale)

					const gradient = ctx.createRadialGradient(0, 0, 0, 0, 0, noteHeight)
					gradient.addColorStop(
						0,
						note.hitType === 'perfect' ? '#ffd700' : '#c0c0c0'
					)
					gradient.addColorStop(1, 'transparent')
					ctx.fillStyle = gradient
					ctx.fillRect(-noteWidth / 2, -noteHeight / 2, noteWidth, noteHeight)

					ctx.restore()
				} else {
					ctx.save()

					ctx.shadowColor = color
					ctx.shadowBlur = 15

					ctx.fillStyle = color
					ctx.beginPath()
					ctx.roundRect(perspXStart, note.y, noteWidth, noteHeight, perspRadius)
					ctx.fill()

					const innerGradient = ctx.createLinearGradient(
						perspXStart,
						note.y,
						perspXStart,
						note.y + noteHeight
					)
					innerGradient.addColorStop(0, 'rgba(255, 255, 255, 0.4)')
					innerGradient.addColorStop(0.5, 'rgba(255, 255, 255, 0.1)')
					innerGradient.addColorStop(1, 'rgba(0, 0, 0, 0.2)')
					ctx.fillStyle = innerGradient
					ctx.beginPath()
					ctx.roundRect(
						perspXStart + 2,
						note.y + 2,
						noteWidth - 4,
						noteHeight - 4,
						Math.max(1, perspRadius - 2)
					)
					ctx.fill()

					ctx.restore()
				}
			}

			// Draw hit effects
			for (const effect of hitEffectsRef.current) {
				const elapsed = now - effect.time
				const progress = elapsed / HIT_EFFECT_DURATION
				const alpha = 1 - progress

				const x = effect.lane * LANE_WIDTH + LANE_WIDTH / 2
				const y = HIT_ZONE_Y + HIT_ZONE_HEIGHT / 2

				ctx.save()
				ctx.globalAlpha = alpha

				if (effect.type === 'perfect') {
					const sparkleSize = 30 + progress * 20
					const gradient = ctx.createRadialGradient(x, y, 0, x, y, sparkleSize)
					gradient.addColorStop(0, '#ffd700')
					gradient.addColorStop(0.5, '#ffaa00')
					gradient.addColorStop(1, 'transparent')
					ctx.fillStyle = gradient
					ctx.beginPath()
					ctx.arc(x, y, sparkleSize, 0, Math.PI * 2)
					ctx.fill()

					ctx.fillStyle = '#ffd700'
					ctx.font = `bold 16px ${GAME_FONT_FAMILY}`
					ctx.textAlign = 'center'
					ctx.fillText('PERFECTO!', x, y - 30 - progress * 20)
				} else if (effect.type === 'good') {
					const sparkleSize = 20 + progress * 15
					const gradient = ctx.createRadialGradient(x, y, 0, x, y, sparkleSize)
					gradient.addColorStop(0, '#c0c0c0')
					gradient.addColorStop(0.5, '#888888')
					gradient.addColorStop(1, 'transparent')
					ctx.fillStyle = gradient
					ctx.beginPath()
					ctx.arc(x, y, sparkleSize, 0, Math.PI * 2)
					ctx.fill()

					ctx.fillStyle = '#c0c0c0'
					ctx.font = `bold 14px ${GAME_FONT_FAMILY}`
					ctx.textAlign = 'center'
					ctx.fillText('BIEN', x, y - 25 - progress * 15)
				} else {
					ctx.fillStyle = '#ff4444'
					ctx.font = `bold 14px ${GAME_FONT_FAMILY}`
					ctx.textAlign = 'center'
					ctx.fillText('FALLO', x, y - 20 - progress * 10)
				}

				ctx.restore()
			}

			// Draw combo counter in canvas (top-right)
			if (comboRef.current > 1) {
				ctx.save()

				// Position in top-right corner
				const comboX = CANVAS_WIDTH - 15
				const comboY = 35

				// Background pill (wider to fit "COMBO")
				ctx.fillStyle = 'rgba(202, 138, 4, 0.6)' // yellow-600 with transparency
				ctx.beginPath()
				ctx.roundRect(comboX - 95, comboY - 22, 105, 44, 8)
				ctx.fill()

				// Border
				ctx.strokeStyle = '#eab308' // yellow-500
				ctx.lineWidth = 2
				ctx.stroke()

				// "COMBO" label
				ctx.fillStyle = '#fbbf24' // yellow-400
				ctx.font = `bold 10px ${GAME_FONT_FAMILY}`
				ctx.textAlign = 'right'
				ctx.textBaseline = 'middle'
				ctx.fillText('COMBO', comboX - 8, comboY - 8)

				// Multiplier number
				ctx.fillStyle = '#fde047' // yellow-300
				ctx.font = `bold 18px ${GAME_FONT_FAMILY}`
				ctx.fillText(`x${comboRef.current}`, comboX - 8, comboY + 10)

				ctx.restore()
			}
		}

		animationFrameId.current = requestAnimationFrame(gameLoop)

		return () => {
			if (animationFrameId.current) {
				cancelAnimationFrame(animationFrameId.current)
			}
		}
	// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		gameStarted,
		gamePaused,
		quitGame,
		laneColors,
		audioInitialized,
		getLevelConfig,
		triggerLevelUp,
	])

	// Start game
	const startGame = async () => {
		if (!audioInitialized) {
			await audioManager.init()
			setAudioInitialized(true)
		}

		setInstructionsStep(0)
		setLevel(1)
		setPoints(0)
		setCombo(0)
		setMaxCombo(0)
		setCurrentLives(initialLives)
		notesRef.current = []
		hitEffectsRef.current = []
		gameTimeRef.current = 0
		lastBeatRef.current = -1
		noteIdCounterRef.current = 0
		songLoopCountRef.current = 0
		songNoteIndexRef.current = 0
		setGamePaused(false)
		setQuitGame(false)
		setShowLevelUp(false)
		setGameStarted(true)

		// Get initial song and start background beat
		const initialConfig = getLevelConfig(1)
		currentConfigRef.current = initialConfig // Cache the config to prevent random song changes
		const initialSong = initialConfig.song
		currentSongRef.current = initialSong
		const initialBpm = initialSong.metadata.bpm

		// Start background beat with song's BPM and drum pattern
		if (initialSong.background.drumPattern !== 'none') {
			audioManager.startBackgroundBeat(
				initialBpm,
				initialSong.background.drumPattern as 'chill' | 'default' | 'upbeat',
				initialConfig.cycle
			)
		}
	}

	const cancelQuit = () => {
		setQuitGame(false)
		setGamePaused(wasGamePaused)
	}

	const handleQuitConfirm = () => {
		audioManager.stopBackgroundBeat()
		setPointsEarned(points)
		setGameOver(true)
		setGameStarted(false)
		if (animationFrameId.current) {
			cancelAnimationFrame(animationFrameId.current)
		}
	}

	const toggleMute = () => {
		const newMuted = !audioMuted
		setAudioMuted(newMuted)
		audioManager.setMuted(newMuted)
	}

	return (
		<div
			className="game-container"
			style={{ backgroundColor: brandColor }}
		>
			{/* Game Header */}
			<GameHeader
				level={level}
				lives={currentLives}
				score={points}
				onPause={
					gameStarted && !quitGame && !showLevelUp
						? () => setGamePaused(!gamePaused)
						: undefined
				}
				isPaused={gamePaused}
				brandColor={brandColor}
				isMobile={mobile}
				onQuit={() => {
					setQuitGame(true)
					setWasGamePaused(gamePaused)
					setGamePaused(true)
				}}
				showQuitButton={gameStarted}
				isQuitting={quitGame}
			/>

			{/* Game Container */}
			<div className="game-board">
				<div
					className="game-board-inner"
					style={{ display: 'flex', flexDirection: 'column', width: '100%', minHeight: '600px', maxWidth: '32rem', margin: '0 auto', backgroundColor: brandColor }}
				>
					{/* Game Canvas */}
					<GameCanvas
						ref={canvasRef}
						width={CANVAS_WIDTH}
						height={CANVAS_HEIGHT}
						className="max-w-full max-h-full"
					/>

					{/* Touch controls overlay */}
					{mobile && gameStarted && !gamePaused && !quitGame && (
						<div
							style={{ position: 'absolute', inset: 0, display: 'flex', pointerEvents: 'auto' }}
						>
							{[0, 1, 2, 3].map(lane => (
								<button
									key={lane}
									style={{ flex: 1, height: '100%', opacity: 0 }}
									onTouchStart={e => {
										e.preventDefault()
										handleLaneTouch(lane)
									}}
								/>
							))}
						</div>
					)}

					{/* Start Screen */}
					{!gameStarted && (
						<GameOverlay
							type="custom"
							title={` ${gameTitle}`}
							brandColor={brandColor}
							onAction={
								instructionsStep === 0
									? () => setInstructionsStep(1)
									: startGame
							}
							actionText={instructionsStep === 0 ? 'Siguiente ' : ' JUGAR'}
							showPressToStart={false}
							customContent={
								<div className="space-y-3">
									{brandLogo && (
										<>
											<span className="text-xs text-white">por:</span>
											<img
												src={brandLogo}
												alt="Brand Logo"
												className="mx-auto w-48 h-auto !mb-4"
											/>
										</>
									)}
									{instructionsStep === 0 ? (
										<>
											<div className="flex justify-center items-center gap-x-3 text-xs text-gray-300">
												{Object.entries(DEFAULT_LANE_COLORS).map(
													([key, color], i) => (
														<div
															key={key}
															className="flex flex-col items-center gap-1"
														>
															<div
																className="w-8 h-6 rounded"
																style={{ backgroundColor: color }}
															/>
															<span>{LANE_LABELS[i]}</span>
														</div>
													)
												)}
											</div>
											<div className="text-center text-sm text-gray-300 mt-4">
												{mobile ? (
													<>
														<p> Toca los carriles cuando las notas</p>
														<p>lleguen a la lnea blanca</p>
													</>
												) : (
													<>
														<p> Usa D, F, J, K para los 4 carriles</p>
														<p> ESPACIO o P para pausar</p>
													</>
												)}
											</div>
											<button
												onClick={startGame}
												className="text-xs text-gray-400 hover:text-white underline mt-2"
											>
												Omitir
											</button>
										</>
									) : (
										<>
											<div className="text-sm text-gray-300 space-y-1">
												<div className="text-xs text-gray-400 mt-3 p-2 bg-white/10 rounded">
													<p>
														 Golpea las notas en el momento exacto
														<br />
														Cada cancin tiene su meloda!
														<br />
														Conforme avanzas se volver ms dificil
													</p>
												</div>
												<p> PERFECTO = 50 pts  combo</p>
												<p> BIEN = 25 pts  combo</p>
												<p> FALLO = pierdes una vida</p>
												<p> Mantn el combo para ms puntos!</p>
											</div>
											<button
												onClick={() => setInstructionsStep(0)}
												className="text-xs text-gray-400 hover:text-white underline mt-2"
											>
												 Atrs
											</button>
										</>
									)}
								</div>
							}
						/>
					)}

					{/* Level Up Overlay */}
					{showLevelUp && nextLevelConfigRef.current && (
						<GameOverlay
							type="custom"
							title=" NIVEL COMPLETADO!"
							message={`Pasaste al Nivel ${level + 1}!`}
							brandColor={brandColor}
							customContent={
								<div className="text-center">
									<p className="text-lg text-yellow-400 font-bold mb-2">
										 {nextLevelConfigRef.current.song.metadata.title}
									</p>
									<p className="text-sm text-gray-300 mb-1">
										por {nextLevelConfigRef.current.song.metadata.artist}
									</p>
									<p className="text-sm text-gray-400">
										{nextLevelConfigRef.current.cycle > 0
											? `Velocidad x${nextLevelConfigRef.current.cycle + 1}!`
											: `Nueva meloda: ${DIFFICULTY_NAMES[nextLevelConfigRef.current.difficulty]}`}
									</p>
								</div>
							}
						/>
					)}

					{/* Pause Overlay */}
					{gamePaused && !quitGame && !showLevelUp && (
						<GameOverlay
							type="pause"
							message={`Nivel ${level}: ${currentConfig.displayName}`}
							brandColor={brandColor}
							onAction={() => setGamePaused(false)}
							actionText=" CONTINUAR"
							customContent={
								<button
									onClick={toggleMute}
									className="mt-4 px-4 py-2 bg-gray-700 text-white rounded text-sm"
								>
									{audioMuted ? ' Sonido desactivado' : ' Sonido activado'}
								</button>
							}
						/>
					)}

					{/* Quit Confirmation Overlay */}
					{quitGame && (
						<GameOverlay
							type="custom"
							title="Salir del Juego?"
							message={`Nivel ${level}  ${points} puntos`}
							brandColor={brandColor}
							customContent={
								<div className="game-quit-buttons">
									<button
										onClick={cancelQuit}
										className="game-quit-btn game-quit-btn-cancel"
									>
										 Cancelar
									</button>
									<button
										onClick={handleQuitConfirm}
										className="game-quit-btn game-quit-btn-confirm"
									>
										 Salir
									</button>
								</div>
							}
						/>
					)}
				</div>
			</div>

			{/* Stats bar when playing - always render container for consistent layout */}
			<div style={{ display: 'flex', justifyContent: 'center', gap: '1rem', padding: '0.5rem', backgroundColor: 'rgba(0,0,0,0.5)', color: 'white', fontSize: '0.75rem', minHeight: '32px' }}>
				{gameStarted && (
					<>
						<span>
							{' '}
							{currentConfigRef.current?.displayName ||
								currentConfig.displayName}
						</span>
						<span>|</span>
						<span>Combo mximo: {maxCombo}</span>
					</>
				)}
			</div>
		</div>
	)
}

export default RhythmHighway
