import React from 'react'
import useFlappyAnimation from './hooks/useFlappyAnimation'
import useCanvas from './hooks/useCanvas'
import { useImageLoader } from './hooks/useImageLoader'
import { GameCanvas } from '../ui/GameCanvas'

interface FlappyProps {
	lives: number
	setPointsEarned: (points: number) => void
	setGameOver: (isOver: boolean) => void
	skin?: string
	gameTitle?: string
	hideOverlays?: boolean
	onScoreUpdate?: (score: number) => void
	onLivesUpdate?: (lives: number) => void
	isPaused?: boolean
	maxPoints?: number
}

const Flappy = ({
	lives,
	setGameOver,
	setPointsEarned,
	skin,
	gameTitle = 'FLAPPY CAT',
	hideOverlays = false,
	onScoreUpdate,
	onLivesUpdate,
	isPaused = false,
	maxPoints,
}: FlappyProps) => {
	const [images, loading, error] = useImageLoader(skin)

	const config = React.useMemo(() => {
		if (!images) return null

		return {
			assets: images,
			options: {
				gameTitle: gameTitle,
				delayFrameCount: 5,
				birdX: 50,
				gap: 480,
				upMovement: 18,
				gravity: 2.5,
				pointsPerObstacle: 10,
				lives: lives,
				hideOverlays: hideOverlays,
				isPaused: isPaused,
				maxPoints: maxPoints,
			},
			functions: {
				setGameOver,
				setPointsEarned,
				onScoreUpdate,
				onLivesUpdate,
			},
		}
	}, [
		images,
		gameTitle,
		lives,
		setGameOver,
		setPointsEarned,
		hideOverlays,
		onScoreUpdate,
		onLivesUpdate,
		isPaused,
		maxPoints,
	])

	const [tap, draw] = useFlappyAnimation(config)
	const canvasRef = useCanvas(draw)

	const handleClick = () => {
		if (!config) return

		const canvas = canvasRef.current
		const ctx = canvas?.getContext('2d')

		if (ctx) {
			tap()
		}
	}

	if (loading) {
		return (
			<div className="flex items-center justify-center w-72 h-[512px] bg-slate-800 rounded-lg">
				<div className="text-white text-center">
					<div className="animate-spin rounded-full h-12 w-12 border-b-2 border-white mx-auto mb-4"></div>
					<p>Cargando juego...</p>
				</div>
			</div>
		)
	}

	if (error) {
		return (
			<div className="flex items-center justify-center w-72 h-[512px] bg-red-900 rounded-lg">
				<div className="text-white text-center p-4">
					<p className="font-bold mb-2">Error al cargar el juego</p>
					<p className="text-sm">{error}</p>
				</div>
			</div>
		)
	}

	return (
		<GameCanvas
			ref={canvasRef}
			width={288}
			height={512}
			onClick={handleClick}
			className="cursor-pointer rounded-lg shadow-xl"
		/>
	)
}

export default Flappy
