import * as Tone from 'tone'

export class BreakoutAudioManager {
	private initialized = false
	private muted = false

	// Timing protection
	private lastBounceTime = 0
	private readonly MIN_BOUNCE_GAP = 0.03

	// Synthesizers
	private bounceSynth: Tone.Synth | null = null
	private brickSynth: Tone.PolySynth | null = null
	private powerUpSynth: Tone.PolySynth | null = null
	private loseSynth: Tone.Synth | null = null
	private levelSynth: Tone.PolySynth | null = null

	async init(): Promise<void> {
		if (this.initialized) return

		await Tone.start()

		// Bounce synth - for paddle and wall hits
		this.bounceSynth = new Tone.Synth({
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.05,
				sustain: 0,
				release: 0.05,
			},
			volume: -10,
		}).toDestination()

		// Brick synth - satisfying crunch for brick break
		this.brickSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'square' },
			envelope: {
				attack: 0.001,
				decay: 0.08,
				sustain: 0,
				release: 0.05,
			},
			volume: -12,
		}).toDestination()

		// Power-up synth - chime
		this.powerUpSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.01,
				decay: 0.2,
				sustain: 0.1,
				release: 0.4,
			},
			volume: -8,
		}).toDestination()

		// Lose life synth - sad tone
		this.loseSynth = new Tone.Synth({
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.3,
				sustain: 0,
				release: 0.3,
			},
			volume: -8,
		}).toDestination()

		// Level up synth - fanfare
		this.levelSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.2,
				sustain: 0.3,
				release: 0.5,
			},
			volume: -8,
		}).toDestination()

		this.initialized = true
	}

	// Paddle hit - low bounce blip
	playPaddleHit(): void {
		if (!this.initialized || this.muted || !this.bounceSynth) return

		const now = Tone.now()
		if (now - this.lastBounceTime < this.MIN_BOUNCE_GAP) return

		this.lastBounceTime = now
		this.bounceSynth.triggerAttackRelease('G4', '32n', now)
	}

	// Wall hit - higher bounce
	playWallHit(): void {
		if (!this.initialized || this.muted || !this.bounceSynth) return

		const now = Tone.now()
		if (now - this.lastBounceTime < this.MIN_BOUNCE_GAP) return

		this.lastBounceTime = now
		this.bounceSynth.triggerAttackRelease('C5', '32n', now)
	}

	// Brick break - satisfying crunch
	playBrickBreak(): void {
		if (!this.initialized || this.muted || !this.brickSynth) return
		const now = Tone.now()
		// Quick two-note crunch
		this.brickSynth.triggerAttackRelease('E5', '32n', now)
		this.brickSynth.triggerAttackRelease('G4', '32n', now + 0.02)
	}

	// Power-up collection chime
	playPowerUpCollect(): void {
		if (!this.initialized || this.muted || !this.powerUpSynth) return
		const now = Tone.now()
		// Ascending chime
		const notes = ['C5', 'E5', 'G5', 'C6']
		notes.forEach((note, i) => {
			this.powerUpSynth!.triggerAttackRelease(note, '16n', now + i * 0.06)
		})
	}

	// Lose life - sad descending tone
	playLoseLife(): void {
		if (!this.initialized || this.muted || !this.loseSynth) return
		const now = Tone.now()
		// Descending sad phrase
		const notes = ['E4', 'D4', 'C4']
		notes.forEach((note, i) => {
			this.loseSynth!.triggerAttackRelease(note, '8n', now + i * 0.2)
		})
	}

	// Level up - triumphant fanfare
	playLevelUp(): void {
		if (!this.initialized || this.muted || !this.levelSynth) return
		const now = Tone.now()
		// Ascending fanfare with final chord
		const arpeggio = ['C4', 'E4', 'G4', 'C5']
		arpeggio.forEach((note, i) => {
			this.levelSynth!.triggerAttackRelease(note, '16n', now + i * 0.08)
		})
		// Final triumphant chord
		this.levelSynth.triggerAttackRelease(['C5', 'E5', 'G5'], '4n', now + 0.4)
	}

	setMuted(muted: boolean): void {
		this.muted = muted
	}

	isMuted(): boolean {
		return this.muted
	}

	dispose(): void {
		if (!this.initialized) return

		this.bounceSynth?.dispose()
		this.brickSynth?.dispose()
		this.powerUpSynth?.dispose()
		this.loseSynth?.dispose()
		this.levelSynth?.dispose()

		this.bounceSynth = null
		this.brickSynth = null
		this.powerUpSynth = null
		this.loseSynth = null
		this.levelSynth = null

		this.initialized = false
	}
}

export const breakoutAudio = new BreakoutAudioManager()
