import type {
	Ball,
	Brick,
	Paddle,
	PowerUpType,
	CollisionResult,
} from '../types'

export function collisionDetection(
	ball: Ball,
	bricks: Brick[][],
	paddle: Paddle,
	canvasHeight: number,
	canvasWidth: number
): CollisionResult {
	let pointsToAdd = 0
	let lifeLost = false
	let spawnPowerUp: { x: number; y: number; type: PowerUpType } | undefined
	const nextBallX = ball.x + ball.dx
	const nextBallY = ball.y + ball.dy

	// Brick collision
	for (let c = 0; c < bricks.length; c++) {
		for (let r = 0; r < bricks[c].length; r++) {
			const brick = bricks[c][r]
			if (brick.status > 0) {
				if (
					nextBallX + ball.radius > brick.x &&
					nextBallX - ball.radius < brick.x + brick.width &&
					nextBallY + ball.radius > brick.y &&
					nextBallY - ball.radius < brick.y + brick.height
				) {
					brick.status -= 1

					// Award points only when brick is fully broken
					if (brick.status === 0) {
						pointsToAdd += brick.points
						// Spawn power-up if this brick had one
						if (brick.hasPowerUp && brick.powerUpType) {
							spawnPowerUp = {
								x: brick.x + brick.width / 2,
								y: brick.y + brick.height / 2,
								type: brick.powerUpType,
							}
						}
					}

					// Simplified bounce logic: prioritize vertical bounce if mostly vertical collision
					const overlapX = Math.min(
						ball.x + ball.radius - brick.x,
						brick.x + brick.width - (ball.x - ball.radius)
					)
					const overlapY = Math.min(
						ball.y + ball.radius - brick.y,
						brick.y + brick.height - (ball.y - ball.radius)
					)

					if (overlapY <= overlapX) {
						// More horizontal overlap or equal -> Vertical bounce
						ball.dy = -ball.dy
						// Correct position slightly to prevent sticking
						ball.y += ball.dy > 0 ? -overlapY : overlapY
					} else {
						// More vertical overlap -> Horizontal bounce
						ball.dx = -ball.dx
						// Correct position slightly
						ball.x += ball.dx > 0 ? -overlapX : overlapX
					}

					return { pointsToAdd, lifeLost, spawnPowerUp } // Return after first hit
				}
			}
		}
	}

	// Paddle collision
	if (
		nextBallY + ball.radius > paddle.y && // Ball bottom below paddle top
		ball.y - ball.radius < paddle.y + paddle.height && // Ball top above paddle bottom (prevents hitting from below)
		nextBallX + ball.radius > paddle.x && // Ball right edge right of paddle left
		nextBallX - ball.radius < paddle.x + paddle.width // Ball left edge left of paddle right
	) {
		// Only trigger if ball is moving downwards
		if (ball.dy > 0) {
			let collidePoint =
				(ball.x - (paddle.x + paddle.width / 2)) / (paddle.width / 2)
			collidePoint = Math.max(-0.95, Math.min(0.95, collidePoint)) // Clamp value
			const angleRad = collidePoint * (Math.PI / 3) // Max angle 60 degrees
			ball.dx = ball.speed * Math.sin(angleRad)
			ball.dy = -Math.abs(ball.speed * Math.cos(angleRad)) // Ensure always upwards
			ball.y = paddle.y - ball.radius - 0.1 // Move ball slightly above paddle
		}
	}

	// Wall collision (left/right)
	if (nextBallX > canvasWidth - ball.radius || nextBallX < ball.radius) {
		ball.dx = -ball.dx
		// Correct position
		if (nextBallX > canvasWidth - ball.radius) {
			ball.x = canvasWidth - ball.radius
		} else {
			ball.x = ball.radius
		}
	}

	// Top wall collision
	if (nextBallY < ball.radius) {
		ball.dy = -ball.dy
		ball.y = ball.radius
	}

	// Bottom wall (life lost)
	if (ball.y > canvasHeight + ball.radius) {
		lifeLost = true
	}

	return { pointsToAdd, lifeLost, spawnPowerUp }
}
