import * as Tone from 'tone'

export class FlappyBirdAudioManager {
	private initialized = false
	private muted = false

	// Timing protection
	private lastFlapTime = 0
	private readonly MIN_FLAP_GAP = 0.08

	// Synthesizers
	private flapSynth: Tone.Synth | null = null
	private scoreSynth: Tone.PolySynth | null = null
	private collisionSynth: Tone.MembraneSynth | null = null
	private gameOverSynth: Tone.PolySynth | null = null

	async init(): Promise<void> {
		if (this.initialized) return

		await Tone.start()

		// Flap synth - quick sine sweep up
		this.flapSynth = new Tone.Synth({
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.08,
				sustain: 0,
				release: 0.05,
			},
			volume: -10,
		}).toDestination()

		// Score synth - bell chime
		this.scoreSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.01,
				decay: 0.15,
				sustain: 0.1,
				release: 0.3,
			},
			volume: -8,
		}).toDestination()

		// Collision synth - impact thud
		this.collisionSynth = new Tone.MembraneSynth({
			pitchDecay: 0.03,
			octaves: 3,
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.2,
				sustain: 0,
				release: 0.1,
			},
			volume: -6,
		}).toDestination()

		// Game over synth - descending melody
		this.gameOverSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.25,
				sustain: 0,
				release: 0.3,
			},
			volume: -8,
		}).toDestination()

		this.initialized = true
	}

	// Quick upward sweep for flap
	playFlap(): void {
		if (!this.initialized || this.muted || !this.flapSynth) return

		const now = Tone.now()
		if (now - this.lastFlapTime < this.MIN_FLAP_GAP) return

		this.lastFlapTime = now
		// Quick ascending notes for wing flap feel
		this.flapSynth.triggerAttackRelease('C5', '32n', now)
		this.flapSynth.triggerAttackRelease('E5', '32n', now + 0.03)
	}

	// Bell chime for scoring
	playScore(): void {
		if (!this.initialized || this.muted || !this.scoreSynth) return
		const now = Tone.now()
		// Pleasant ding
		this.scoreSynth.triggerAttackRelease(['E5', 'G5'], '8n', now)
	}

	// Impact thud for collision
	playCollision(): void {
		if (!this.initialized || this.muted || !this.collisionSynth) return
		const now = Tone.now()
		this.collisionSynth.triggerAttackRelease('C2', '8n', now)
	}

	// Descending melody for game over
	playGameOver(): void {
		if (!this.initialized || this.muted || !this.gameOverSynth) return
		const now = Tone.now()

		// Sad descending melody
		const notes = ['E4', 'D4', 'C4', 'B3', 'G3']
		notes.forEach((note, i) => {
			this.gameOverSynth!.triggerAttackRelease(note, '8n', now + i * 0.18)
		})
	}

	setMuted(muted: boolean): void {
		this.muted = muted
	}

	isMuted(): boolean {
		return this.muted
	}

	dispose(): void {
		if (!this.initialized) return

		this.flapSynth?.dispose()
		this.scoreSynth?.dispose()
		this.collisionSynth?.dispose()
		this.gameOverSynth?.dispose()

		this.flapSynth = null
		this.scoreSynth = null
		this.collisionSynth = null
		this.gameOverSynth = null

		this.initialized = false
	}
}

export const flappyBirdAudio = new FlappyBirdAudioManager()
