import React, { useEffect, useState } from 'react'

interface GameOverlayProps {
	type: 'start' | 'pause' | 'gameOver' | 'levelComplete' | 'custom'
	title?: string
	message?: string
	score?: number
	level?: number
	brandColor?: string
	onAction?: () => void
	actionText?: string
	customContent?: React.ReactNode
	showPressToStart?: boolean
	lang?: string
}

export const GameOverlay: React.FC<GameOverlayProps> = ({
	type,
	title,
	message,
	score,
	level,
	brandColor = '#00d26a',
	onAction,
	actionText,
	customContent,
	showPressToStart = true,
}) => {
	const [showBlink, setShowBlink] = useState(true)

	useEffect(() => {
		if (showPressToStart && type === 'start') {
			const interval = setInterval(() => {
				setShowBlink(prev => !prev)
			}, 500)
			return () => clearInterval(interval)
		}
	}, [showPressToStart, type])

	const defaultTitles = {
		start: ' LISTO PARA JUGAR',
		pause: ' PAUSA',
		gameOver: ' GAME OVER',
		levelComplete: ' NIVEL COMPLETADO!',
		custom: '',
	}

	const displayTitle = title || defaultTitles[type]

	return (
		<div className="game-overlay">
			{/* Background Overlay */}
			<div className="game-overlay-bg" />

			{/* Content Card */}
			<div className="game-overlay-card">
				{/* Decorative Border Glow */}
				<div
					className="game-overlay-glow"
					style={{ backgroundColor: brandColor }}
				/>

				<div className="game-overlay-content">
					{/* Title */}
					{displayTitle && (
						<h2 className="game-overlay-title">{displayTitle}</h2>
					)}

					{/* Score Display */}
					{score !== undefined && (
						<div className="game-overlay-score">
							<p className="game-overlay-score-label">Puntuacin Final</p>
							<p className="game-overlay-score-value">
								{score.toLocaleString()}
							</p>
						</div>
					)}

					{/* Level Display */}
					{level !== undefined && type === 'levelComplete' && (
						<div className="game-overlay-level">
							<p className="game-overlay-level-label">Nivel</p>
							<p className="game-overlay-level-value">{level}</p>
						</div>
					)}

					{/* Custom Message */}
					{message && (
						<p className="game-overlay-message">{message}</p>
					)}

					{/* Custom Content */}
					{customContent}

					{/* Action Button or Press to Start */}
					{type === 'start' && showPressToStart && !onAction ? (
						<p
							className="game-overlay-blink"
							style={{
								color: brandColor,
								opacity: showBlink ? 1 : 0,
							}}
						>
							PRESIONA ESPACIO PARA COMENZAR
						</p>
					) : onAction && actionText ? (
						<button
							onClick={onAction}
							className="game-overlay-button"
							style={{
								backgroundColor: brandColor,
							}}
						>
							{actionText}
						</button>
					) : null}
				</div>
			</div>
		</div>
	)
}
