import React from 'react'

interface GameControlsProps {
	onControl: (
		direction: 'up' | 'down' | 'left' | 'right' | 'action' | 'action2'
	) => void
	onControlEnd?: (
		direction: 'up' | 'down' | 'left' | 'right' | 'action' | 'action2'
	) => void
	brandColor?: string
	showActionButton?: boolean
	actionLabel?: string
	layout?: 'dpad' | 'arrows' | 'horizontal' | 'racing'
	actionIcon?: React.ReactNode
	action2Label?: string
	action2Icon?: React.ReactNode
}

export const GameControls: React.FC<GameControlsProps> = ({
	onControl,
	onControlEnd,
	showActionButton = false,
	layout = 'dpad',
	actionLabel = 'A',
	actionIcon,
	action2Label = 'B',
	action2Icon,
}) => {
	// Common styles for preventing text selection
	const preventSelectStyles: React.CSSProperties = {
		userSelect: 'none',
		WebkitUserSelect: 'none',
		MozUserSelect: 'none',
		msUserSelect: 'none',
		touchAction: 'none',
		WebkitTouchCallout: 'none',
	}

	const handleContextMenu = (e: React.MouseEvent) => {
		e.preventDefault()
		return false
	}

	// Horizontal layout for games like Breakout
	if (layout === 'horizontal') {
		return (
			<div className="game-controls-horizontal">
				<button
					onTouchStart={e => {
						e.preventDefault()
						onControl('left')
					}}
					onClick={() => onControl('left')}
					onContextMenu={handleContextMenu}
					className="game-controls-btn game-controls-btn-horizontal"
					style={preventSelectStyles}
				>
					
				</button>
				{showActionButton && (
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('action')
						}}
						onClick={() => onControl('action')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-horizontal game-controls-btn-action"
						style={preventSelectStyles}
					>
						{actionIcon || actionLabel}
					</button>
				)}
				<button
					onTouchStart={e => {
						e.preventDefault()
						onControl('right')
					}}
					onClick={() => onControl('right')}
					onContextMenu={handleContextMenu}
					className="game-controls-btn game-controls-btn-horizontal"
					style={preventSelectStyles}
				>
					
				</button>
			</div>
		)
	}

	// Racing layout with steering controls on left and action buttons on right
	if (layout === 'racing') {
		return (
			<div className="game-controls-racing">
				{/* Steering controls on the left */}
				<div className="game-controls-racing-steering">
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('left')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('left')
						}}
						onMouseDown={() => onControl('left')}
						onMouseUp={() => onControlEnd?.('left')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-steering"
						style={preventSelectStyles}
					>
						
					</button>
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('right')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('right')
						}}
						onMouseDown={() => onControl('right')}
						onMouseUp={() => onControlEnd?.('right')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-steering"
						style={preventSelectStyles}
					>
						
					</button>
				</div>

				{/* Action buttons on the right */}
				<div className="game-controls-racing-actions">
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('up')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('up')
						}}
						onMouseDown={() => onControl('up')}
						onMouseUp={() => onControlEnd?.('up')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-gas"
						style={preventSelectStyles}
					>
						{actionIcon || actionLabel}
					</button>
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('down')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('down')
						}}
						onMouseDown={() => onControl('down')}
						onMouseUp={() => onControlEnd?.('down')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-brake"
						style={preventSelectStyles}
					>
						{action2Icon || action2Label}
					</button>
				</div>
			</div>
		)
	}

	if (layout === 'arrows') {
		return (
			<div className="game-controls-container">
				<div className="game-controls-dpad">
					{/* Background Circle */}
					<div className="game-controls-dpad-bg" />

					{/* Center Circle */}
					<div className="game-controls-dpad-center" />

					{/* Arrow Buttons */}
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('up')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('up')
						}}
						onMouseDown={() => onControl('up')}
						onMouseUp={() => onControlEnd?.('up')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-up"
						style={preventSelectStyles}
					>
						
					</button>

					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('left')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('left')
						}}
						onMouseDown={() => onControl('left')}
						onMouseUp={() => onControlEnd?.('left')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-left"
						style={preventSelectStyles}
					>
						
					</button>

					{showActionButton && (
						<button
							onTouchStart={e => {
								e.preventDefault()
								onControl('action')
							}}
							onClick={() => onControl('action')}
							onContextMenu={handleContextMenu}
							className="game-controls-btn game-controls-btn-action"
							style={preventSelectStyles}
						>
							{actionIcon || actionLabel}
						</button>
					)}

					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('right')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('right')
						}}
						onMouseDown={() => onControl('right')}
						onMouseUp={() => onControlEnd?.('right')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-right"
						style={preventSelectStyles}
					>
						
					</button>

					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('down')
						}}
						onTouchEnd={e => {
							e.preventDefault()
							onControlEnd?.('down')
						}}
						onMouseDown={() => onControl('down')}
						onMouseUp={() => onControlEnd?.('down')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-down"
						style={preventSelectStyles}
					>
						
					</button>
				</div>
			</div>
		)
	}

	// D-Pad layout
	return (
		<div className="game-controls-container">
			<div className="game-controls-dpad">
				{/* D-Pad Background */}
				<div className="game-controls-dpad-bg" />

				{/* Center Circle */}
				<div className="game-controls-dpad-center" />

				{/* D-Pad Buttons */}
				<button
					onTouchStart={e => {
						e.preventDefault()
						onControl('up')
					}}
					onTouchEnd={e => {
						e.preventDefault()
						onControlEnd?.('up')
					}}
					onMouseDown={() => onControl('up')}
					onMouseUp={() => onControlEnd?.('up')}
					onContextMenu={handleContextMenu}
					className="game-controls-btn game-controls-btn-up"
					style={preventSelectStyles}
				>
					
				</button>

				<button
					onTouchStart={e => {
						e.preventDefault()
						onControl('right')
					}}
					onClick={() => onControl('right')}
					onContextMenu={handleContextMenu}
					className="game-controls-btn game-controls-btn-right"
					style={preventSelectStyles}
				>
					
				</button>

				<button
					onTouchStart={e => {
						e.preventDefault()
						onControl('down')
					}}
					onTouchEnd={e => {
						e.preventDefault()
						onControlEnd?.('down')
					}}
					onMouseDown={() => onControl('down')}
					onMouseUp={() => onControlEnd?.('down')}
					onContextMenu={handleContextMenu}
					className="game-controls-btn game-controls-btn-down"
					style={preventSelectStyles}
				>
					
				</button>

				<button
					onTouchStart={e => {
						e.preventDefault()
						onControl('left')
					}}
					onClick={() => onControl('left')}
					onContextMenu={handleContextMenu}
					className="game-controls-btn game-controls-btn-left"
					style={preventSelectStyles}
				>
					
				</button>

				{/* Action Button */}
				{showActionButton && (
					<button
						onTouchStart={e => {
							e.preventDefault()
							onControl('action')
						}}
						onClick={() => onControl('action')}
						onContextMenu={handleContextMenu}
						className="game-controls-btn game-controls-btn-action"
						style={preventSelectStyles}
					>
						{actionIcon || actionLabel}
					</button>
				)}
			</div>
		</div>
	)
}
