export interface Position {
	x: number
	y: number
}

export type Direction = 'UP' | 'DOWN' | 'LEFT' | 'RIGHT'

export interface Food {
	position: Position
	points: number
	type: 'apple' | 'banana' | 'cherries' | 'grapes' | 'boost' | 'diet'
}

export interface FoodType {
	type: 'apple' | 'banana' | 'cherries' | 'grapes' | 'boost' | 'diet'
	points: number
	specialEffect?: 'boost' | 'diet'
	rarity?: number // 0-1, where 1 is most common
}

export const FOOD_TYPES: FoodType[] = [
	{ type: 'apple', points: 1, rarity: 0.3 },
	{ type: 'banana', points: 2, rarity: 0.25 },
	{ type: 'cherries', points: 3, rarity: 0.2 },
	{ type: 'grapes', points: 5, rarity: 0.15 },
	{ type: 'boost', points: 1, specialEffect: 'boost', rarity: 0.05 },
	{ type: 'diet', points: 0, specialEffect: 'diet', rarity: 0.05 },
]

export interface GameState {
	snake: Position[]
	food: Food
	direction: Direction
	nextDirection: Direction
	score: number
	lives: number
	gameStarted: boolean
	gamePaused: boolean
	gameOver: boolean
	speed: number
}

export interface SnakeGameProps {
	images: {
		headUp: HTMLImageElement
		headDown: HTMLImageElement
		headLeft: HTMLImageElement
		headRight: HTMLImageElement
		tail: HTMLImageElement
		apple: HTMLImageElement
		banana: HTMLImageElement
		cherries: HTMLImageElement
		grapes: HTMLImageElement
		boost: HTMLImageElement
		diet: HTMLImageElement
		background: HTMLImageElement
	}
	onScoreUpdate: (score: number) => void
	onLivesUpdate: (lives: number) => void
	onGameOver: () => void
	isPaused: boolean
	boardSize?: number
	cellSize?: number
}
