import React from 'react';
import { Language } from '../../../types/i18n';

interface SettingsDropdownProps {
  lang: Language;
  mode: 'night' | 'day' | '';
  onSelectLang: (lang: Language) => void;
  onSelectMode: (mode: 'night' | 'day') => void;
  onChangeName: () => void;
  onShowWelcome: () => void;
  translations: {
    language: string;
    theme: string;
    dayMode: string;
    nightMode: string;
    changeName: string;
    showWelcome: string;
  };
}

const SettingsDropdown: React.FC<SettingsDropdownProps> = ({
  lang,
  mode,
  onSelectLang,
  onSelectMode,
  onChangeName,
  onShowWelcome,
  translations,
}) => {
  return (
    <div className="settings-dropdown">
      <div className="settings-section">
        <div className="settings-section-header">{translations.language}</div>
        <button
          className={lang === 'en' ? 'active' : ''}
          onClick={() => onSelectLang('en')}
        >
          English
        </button>
        <button
          className={lang === 'es' ? 'active' : ''}
          onClick={() => onSelectLang('es')}
        >
          Espaol
        </button>
      </div>
      <div className="settings-separator" />
      <div className="settings-section">
        <div className="settings-section-header">{translations.theme}</div>
        <button
          className={mode === 'day' ? 'active' : ''}
          onClick={() => onSelectMode('day')}
        >
          {translations.dayMode}
        </button>
        <button
          className={mode === 'night' ? 'active' : ''}
          onClick={() => onSelectMode('night')}
        >
          {translations.nightMode}
        </button>
      </div>
      <div className="settings-separator" />
      <button onClick={onChangeName}>{translations.changeName}</button>
      <div className="settings-separator" />
      <button onClick={onShowWelcome}>{translations.showWelcome}</button>
    </div>
  );
};

export default SettingsDropdown;
