import type { Brick, BrickType, PowerUpType } from '../types'
import {
	BRICK_COLUMN_COUNT,
	BRICK_HEIGHT,
	BRICK_OFFSET_TOP,
	BRICK_PADDING,
	BRICK_ROW_COUNT,
	BRICK_TYPES,
	BRICK_WIDTH,
	CANVAS_WIDTH,
	LEVEL_THEMES,
	POWER_UP_DROP_CHANCE,
	PADDLE_MIN_WIDTH,
	PADDLE_INITIAL_WIDTH,
	PADDLE_WIDTH_REDUCTION_PER_LEVEL,
	BALL_MAX_SPEED,
	BALL_INITIAL_SPEED,
	BALL_SPEED_INCREASE_PER_LEVEL,
} from '../constants'

// Helper to determine brick type based on level and position
const getBrickTypeForPosition = (
	row: number,
	level: number
): { type: BrickType; colorOverride?: string } => {
	const theme = LEVEL_THEMES[(level - 1) % LEVEL_THEMES.length]

	// Create patterns based on level
	if (level % 3 === 0) {
		// Checkerboard pattern
		const useStronger = (row % 2 === 0) !== (level % 2 === 0)
		const typeIndex = useStronger
			? Math.min(2 + Math.floor(level / 5), BRICK_TYPES.length - 1)
			: Math.min(1 + Math.floor(level / 8), BRICK_TYPES.length - 2)
		return {
			type: BRICK_TYPES[typeIndex],
			colorOverride: theme.brickColors[row % theme.brickColors.length],
		}
	} else if (level % 5 === 0) {
		// Diamond pattern (stronger in middle)
		const distanceFromCenter = Math.abs(row - Math.floor(BRICK_ROW_COUNT / 2))
		const typeIndex = Math.max(
			0,
			Math.min(
				BRICK_TYPES.length - 1,
				BRICK_TYPES.length - 1 - distanceFromCenter
			)
		)
		return {
			type: BRICK_TYPES[typeIndex],
			colorOverride: theme.brickColors[row % theme.brickColors.length],
		}
	} else {
		// Gradient pattern (stronger at top)
		const strengthIndex = Math.floor((row / BRICK_ROW_COUNT) * 3)
		const baseTypeIndex = Math.min(strengthIndex + Math.floor(level / 4), 3)
		return {
			type: BRICK_TYPES[Math.min(baseTypeIndex, BRICK_TYPES.length - 1)],
			colorOverride: theme.brickColors[row % theme.brickColors.length],
		}
	}
}

// Determine which power-up type to spawn
const getRandomPowerUpType = (): PowerUpType => {
	const types: PowerUpType[] = [
		'paddle-grow',
		'paddle-shrink', // Include negative power-ups for challenge
		'ball-speed',
		'multi-ball',
		'extra-life',
	]

	// Weight the power-ups (positive ones more likely)
	const weights = [30, 10, 25, 25, 10] // paddle-grow: 30%, shrink: 10%, etc.
	const totalWeight = weights.reduce((a, b) => a + b, 0)
	let random = Math.random() * totalWeight

	for (let i = 0; i < types.length; i++) {
		random -= weights[i]
		if (random <= 0) {
			return types[i]
		}
	}

	return types[0] // Fallback
}

export const createBricks = (level: number): Brick[][] => {
	const bricks: Brick[][] = []
	const brickOffsetLeft =
		(CANVAS_WIDTH -
			(BRICK_COLUMN_COUNT * BRICK_WIDTH +
				(BRICK_COLUMN_COUNT - 1) * BRICK_PADDING)) /
		2

	// Increase power-up chance slightly with level
	const powerUpChance = Math.min(POWER_UP_DROP_CHANCE + level * 0.01, 0.3) // Max 30%

	// Initialize columns
	for (let c = 0; c < BRICK_COLUMN_COUNT; c++) {
		bricks[c] = []
	}

	for (let r = 0; r < BRICK_ROW_COUNT; r++) {
		for (let c = 0; c < BRICK_COLUMN_COUNT; c++) {
			const { type, colorOverride } = getBrickTypeForPosition(r, level)

			// Determine if this brick should have a power-up
			const hasPowerUp = Math.random() < powerUpChance
			const powerUpType = hasPowerUp ? getRandomPowerUpType() : undefined

			const x = c * (BRICK_WIDTH + BRICK_PADDING) + brickOffsetLeft
			const y = r * (BRICK_HEIGHT + BRICK_PADDING) + BRICK_OFFSET_TOP

			bricks[c][r] = {
				x,
				y,
				width: BRICK_WIDTH,
				height: BRICK_HEIGHT,
				color: colorOverride || type.color,
				points: type.points,
				status: type.hitsToBreak,
				hitsToBreak: type.hitsToBreak,
				hasPowerUp,
				powerUpType,
			}
		}
	}

	return bricks
}

export const allBricksBroken = (bricks: Brick[][]): boolean => {
	return bricks.every(column => column.every(brick => brick.status === 0))
}

export const calculatePaddleWidth = (level: number): number => {
	return Math.max(
		PADDLE_MIN_WIDTH,
		PADDLE_INITIAL_WIDTH - (level - 1) * PADDLE_WIDTH_REDUCTION_PER_LEVEL
	)
}

export const calculateBallSpeed = (level: number): number => {
	return Math.min(
		BALL_MAX_SPEED,
		BALL_INITIAL_SPEED + (level - 1) * BALL_SPEED_INCREASE_PER_LEVEL
	)
}
