import React from 'react'

interface GameHeaderProps {
	level?: number
	lives?: number
	score: number
	onPause?: () => void
	isPaused?: boolean
	customItems?: React.ReactNode
	brandColor?: string
	isMobile?: boolean
	onQuit?: () => void
	showQuitButton?: boolean
	isQuitting?: boolean
}

export const GameHeader: React.FC<GameHeaderProps> = ({
	level,
	lives,
	score,
	onPause,
	isPaused = false,
	customItems,
	brandColor = '#00d26a',
	isMobile,
	onQuit,
	showQuitButton = false,
	isQuitting = false,
}) => {
	// Show quit button if explicitly enabled
	const shouldShowQuit = showQuitButton && onQuit && !isQuitting

	return (
		<div className="game-header">
			<div className="game-header-stats">
				{level !== undefined && (
					<div className="game-header-stat-box">
						<span className="game-header-stat-label">Nivel</span>
						<span className="game-header-stat-value">{level}</span>
					</div>
				)}

				{lives !== undefined && (
					<div className="game-header-stat-box">
						<span className="game-header-stat-icon"></span>
						<span className="game-header-stat-value">{lives}</span>
					</div>
				)}

				<div className="game-header-stat-box">
					<span className="game-header-stat-icon"></span>
					<span className="game-header-stat-value">
						{score.toLocaleString()}
					</span>
				</div>

				{customItems}
			</div>

			{/* Control Buttons */}
			<div className="game-header-buttons">
				{/* Pause Button - Hide when quit dialog is shown */}
				{onPause && !isQuitting && (
					<button
						onClick={onPause}
						className="game-header-btn"
						style={{ backgroundColor: brandColor }}
					>
						{isPaused
							? ` ${isMobile ? '' : 'Continuar'}`
							: ` ${isMobile ? '' : 'Pausar'}`}
					</button>
				)}

				{/* Quit Button */}
				{shouldShowQuit && (
					<button
						onClick={onQuit}
						className="game-header-btn game-header-quit-btn"
						style={{ backgroundColor: brandColor }}
					>
						{isMobile ? '' : ' Salir'}
					</button>
				)}
			</div>
		</div>
	)
}
