import * as Tone from 'tone'

export type BombSoundType = 'cross' | 'radius'

export class CandyCrushAudioManager {
	private initialized = false
	private muted = false

	// Timing protection for monophonic synths
	private lastBassTime = 0
	private lastNoiseTime = 0
	private lastUiTime = 0
	private readonly MIN_TIME_GAP = 0.015 // 15ms minimum between triggers

	// Synthesizers
	private uiSynth: Tone.Synth | null = null
	private popSynth: Tone.PolySynth | null = null
	private bassSynth: Tone.MembraneSynth | null = null
	private bellSynth: Tone.PolySynth | null = null
	private noiseSynth: Tone.NoiseSynth | null = null
	private melodySynth: Tone.PolySynth | null = null

	async init(): Promise<void> {
		if (this.initialized) return

		// Start audio context (requires user gesture)
		await Tone.start()

		// UI synth - soft clicks (triangle wave for soft sound)
		this.uiSynth = new Tone.Synth({
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.001,
				decay: 0.05,
				sustain: 0,
				release: 0.05,
			},
			volume: -12,
		}).toDestination()

		// Pop synth - match clear sounds (sine for clean pops)
		this.popSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.15,
				sustain: 0,
				release: 0.1,
			},
			volume: -8,
		}).toDestination()

		// Bass synth - deep booms for match5 and bombs
		this.bassSynth = new Tone.MembraneSynth({
			pitchDecay: 0.05,
			octaves: 4,
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.4,
				sustain: 0,
				release: 0.4,
			},
			volume: -6,
		}).toDestination()

		// Bell synth - chimes for combos and score
		this.bellSynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'sine' },
			envelope: {
				attack: 0.001,
				decay: 0.3,
				sustain: 0.1,
				release: 0.5,
			},
			volume: -10,
		}).toDestination()

		// Noise synth - error buzz and explosion texture
		this.noiseSynth = new Tone.NoiseSynth({
			noise: { type: 'pink' },
			envelope: {
				attack: 0.005,
				decay: 0.1,
				sustain: 0,
				release: 0.1,
			},
			volume: -20,
		}).toDestination()

		// Melody synth - win/lose fanfares (triangle for musical quality)
		this.melodySynth = new Tone.PolySynth(Tone.Synth, {
			oscillator: { type: 'triangle' },
			envelope: {
				attack: 0.01,
				decay: 0.2,
				sustain: 0.3,
				release: 0.5,
			},
			volume: -8,
		}).toDestination()

		this.initialized = true
	}

	// Helper to get safe trigger time for monophonic synths
	private getSafeTime(
		lastTime: number,
		synthKey: 'bass' | 'noise' | 'ui'
	): number {
		const now = Tone.now()
		const minNextTime = lastTime + this.MIN_TIME_GAP
		const safeTime = Math.max(now, minNextTime)

		// Update the tracked time
		if (synthKey === 'bass') this.lastBassTime = safeTime
		else if (synthKey === 'noise') this.lastNoiseTime = safeTime
		else if (synthKey === 'ui') this.lastUiTime = safeTime

		return safeTime
	}

	// Soft click when selecting a candy
	playSelect(): void {
		if (!this.initialized || this.muted || !this.uiSynth) return
		const time = this.getSafeTime(this.lastUiTime, 'ui')
		this.uiSynth.triggerAttackRelease('C6', '32n', time)
	}

	// Quick whoosh when swapping candies
	playSwap(): void {
		if (!this.initialized || this.muted || !this.uiSynth) return
		const time1 = this.getSafeTime(this.lastUiTime, 'ui')
		const time2 = time1 + 0.05
		this.lastUiTime = time2 // Update to the last scheduled time

		this.uiSynth.triggerAttackRelease('G5', '32n', time1)
		this.uiSynth.triggerAttackRelease('E5', '32n', time2)
	}

	// Error buzz for invalid move
	playInvalidMove(): void {
		if (!this.initialized || this.muted) return

		const noiseTime = this.getSafeTime(this.lastNoiseTime, 'noise')
		const bassTime = this.getSafeTime(this.lastBassTime, 'bass')

		if (this.noiseSynth) {
			this.noiseSynth.triggerAttackRelease('16n', noiseTime)
		}
		if (this.bassSynth) {
			this.bassSynth.triggerAttackRelease('E2', '16n', bassTime)
		}
	}

	// Light pop for match-3
	playMatch3(): void {
		if (!this.initialized || this.muted || !this.popSynth) return
		this.popSynth.triggerAttackRelease('E5', '16n')
	}

	// Fuller pop with sparkle for match-4
	playMatch4(): void {
		if (!this.initialized || this.muted || !this.popSynth) return
		const now = Tone.now()
		this.popSynth.triggerAttackRelease('C5', '16n', now)
		this.popSynth.triggerAttackRelease('G5', '32n', now + 0.05)
	}

	// Deep boom with chord for match-5+
	playMatch5(): void {
		if (!this.initialized || this.muted) return

		const bassTime = this.getSafeTime(this.lastBassTime, 'bass')

		if (this.bassSynth) {
			this.bassSynth.triggerAttackRelease('C2', '8n', bassTime)
		}
		if (this.bellSynth) {
			this.bellSynth.triggerAttackRelease(
				['C4', 'E4', 'G4'],
				'8n',
				bassTime + 0.05
			)
		}
	}

	// Helper to select appropriate match sound based on length
	playMatchSound(length: number): void {
		if (length >= 5) {
			this.playMatch5()
		} else if (length === 4) {
			this.playMatch4()
		} else {
			this.playMatch3()
		}
	}

	// Explosive sound for bombs
	playBombExplode(type?: BombSoundType): void {
		if (!this.initialized || this.muted) return

		// Get safe times for each synth to avoid timing conflicts in chain reactions
		const bassTime = this.getSafeTime(this.lastBassTime, 'bass')
		const noiseTime = this.getSafeTime(this.lastNoiseTime, 'noise')

		// Bass impact
		if (this.bassSynth) {
			const note = type === 'cross' ? 'C1' : 'E1'
			this.bassSynth.triggerAttackRelease(note, '4n', bassTime)
		}

		// Noise burst
		if (this.noiseSynth) {
			this.noiseSynth.triggerAttackRelease('8n', noiseTime)
		}

		// Sparkle aftermath (bellSynth is polyphonic, no timing issues)
		if (this.bellSynth) {
			const sparkleNotes =
				type === 'cross' ? ['G5', 'B5', 'D6'] : ['E5', 'G5', 'B5']
			this.bellSynth.triggerAttackRelease(sparkleNotes, '16n', bassTime + 0.1)
		}
	}

	// Escalating chimes for combo chains
	playCombo(chainLevel: number): void {
		if (!this.initialized || this.muted || !this.bellSynth) return

		// Base notes that escalate with chain level
		const baseNotes = ['C5', 'E5', 'G5', 'B5', 'D6', 'F6']
		const numNotes = Math.min(chainLevel, baseNotes.length)
		const notes = baseNotes.slice(0, numNotes)

		const now = Tone.now()
		notes.forEach((note, i) => {
			this.bellSynth!.triggerAttackRelease(note, '16n', now + i * 0.08)
		})
	}

	// Coin ding for score popup
	playScorePopup(): void {
		if (!this.initialized || this.muted || !this.bellSynth) return
		this.bellSynth.triggerAttackRelease('G6', '32n')
	}

	// Victory fanfare - ascending arpeggio with final chord
	playWin(): void {
		if (!this.initialized || this.muted || !this.melodySynth) return

		const now = Tone.now()
		const arpeggio = ['C4', 'E4', 'G4', 'C5', 'E5', 'G5']

		// Play ascending arpeggio
		arpeggio.forEach((note, i) => {
			this.melodySynth!.triggerAttackRelease(note, '16n', now + i * 0.1)
		})

		// Final triumphant chord
		this.melodySynth.triggerAttackRelease(
			['C5', 'E5', 'G5', 'C6'],
			'4n',
			now + 0.7
		)
	}

	// Sad descending phrase for lose
	playLose(): void {
		if (!this.initialized || this.muted || !this.melodySynth) return

		const now = Tone.now()
		const phrase = ['E4', 'D4', 'C4', 'B3']

		phrase.forEach((note, i) => {
			this.melodySynth!.triggerAttackRelease(note, '8n', now + i * 0.25)
		})
	}

	// Mute control
	setMuted(muted: boolean): void {
		this.muted = muted
	}

	isMuted(): boolean {
		return this.muted
	}

	// Cleanup all synths
	dispose(): void {
		if (!this.initialized) return

		this.uiSynth?.dispose()
		this.popSynth?.dispose()
		this.bassSynth?.dispose()
		this.bellSynth?.dispose()
		this.noiseSynth?.dispose()
		this.melodySynth?.dispose()

		this.uiSynth = null
		this.popSynth = null
		this.bassSynth = null
		this.bellSynth = null
		this.noiseSynth = null
		this.melodySynth = null

		this.initialized = false
	}
}

// Singleton instance for easy access
export const candyCrushAudio = new CandyCrushAudioManager()
