import type { Ball, Brick, Paddle, PowerUp } from '../types'
import { POWER_UP_CONFIG } from '../constants'

export const drawBall = (ctx: CanvasRenderingContext2D, ball: Ball) => {
	ctx.beginPath()
	ctx.arc(ball.x, ball.y, ball.radius, 0, Math.PI * 2)
	ctx.fillStyle = ball.color
	// Add glow
	ctx.shadowBlur = 15
	ctx.shadowColor = ball.color
	ctx.fill()
	ctx.closePath()
	// Reset shadow
	ctx.shadowBlur = 0
	ctx.shadowColor = 'transparent'
}

export const drawPaddle = (ctx: CanvasRenderingContext2D, paddle: Paddle) => {
	ctx.beginPath()
	const radius = 5 // Rounded corners
	ctx.moveTo(paddle.x + radius, paddle.y)
	ctx.lineTo(paddle.x + paddle.width - radius, paddle.y)
	ctx.quadraticCurveTo(
		paddle.x + paddle.width,
		paddle.y,
		paddle.x + paddle.width,
		paddle.y + radius
	)
	ctx.lineTo(paddle.x + paddle.width, paddle.y + paddle.height - radius)
	ctx.quadraticCurveTo(
		paddle.x + paddle.width,
		paddle.y + paddle.height,
		paddle.x + paddle.width - radius,
		paddle.y + paddle.height
	)
	ctx.lineTo(paddle.x + radius, paddle.y + paddle.height)
	ctx.quadraticCurveTo(
		paddle.x,
		paddle.y + paddle.height,
		paddle.x,
		paddle.y + paddle.height - radius
	)
	ctx.lineTo(paddle.x, paddle.y + radius)
	ctx.quadraticCurveTo(paddle.x, paddle.y, paddle.x + radius, paddle.y)
	ctx.fillStyle = paddle.color
	ctx.fill()
	ctx.closePath()
}

export const drawBricks = (
	ctx: CanvasRenderingContext2D,
	bricks: Brick[][]
) => {
	bricks.forEach(column => {
		column.forEach(brick => {
			if (brick.status > 0) {
				ctx.beginPath()
				const radius = 3 // Subtle rounded corners
				ctx.moveTo(brick.x + radius, brick.y)
				ctx.lineTo(brick.x + brick.width - radius, brick.y)
				ctx.quadraticCurveTo(
					brick.x + brick.width,
					brick.y,
					brick.x + brick.width,
					brick.y + radius
				)
				ctx.lineTo(brick.x + brick.width, brick.y + brick.height - radius)
				ctx.quadraticCurveTo(
					brick.x + brick.width,
					brick.y + brick.height,
					brick.x + brick.width - radius,
					brick.y + brick.height
				)
				ctx.lineTo(brick.x + radius, brick.y + brick.height)
				ctx.quadraticCurveTo(
					brick.x,
					brick.y + brick.height,
					brick.x,
					brick.y + brick.height - radius
				)
				ctx.lineTo(brick.x, brick.y + radius)
				ctx.quadraticCurveTo(brick.x, brick.y, brick.x + radius, brick.y)

				// Adjust opacity based on hits remaining
				const opacity = brick.status / brick.hitsToBreak
				ctx.fillStyle = brick.color
				ctx.globalAlpha = 0.5 + opacity * 0.5 // Min 50% opacity
				ctx.fill()
				ctx.globalAlpha = 1.0

				// Draw cracks for damaged bricks
				if (brick.status < brick.hitsToBreak) {
					ctx.strokeStyle = 'rgba(0,0,0,0.3)'
					ctx.lineWidth = 2
					ctx.stroke()
				}

				// Draw power-up indicator
				if (brick.hasPowerUp && brick.powerUpType) {
					ctx.fillStyle = 'rgba(255,255,255,0.8)'
					ctx.font = '12px Arial'
					ctx.textAlign = 'center'
					ctx.textBaseline = 'middle'
					ctx.fillText(
						'',
						brick.x + brick.width / 2,
						brick.y + brick.height / 2
					)
				}

				ctx.closePath()
			}
		})
	})
}

export const drawExtraBalls = (
	ctx: CanvasRenderingContext2D,
	extraBalls: Ball[]
) => {
	extraBalls.forEach(ball => {
		ctx.beginPath()
		ctx.arc(ball.x, ball.y, ball.radius, 0, Math.PI * 2)
		ctx.fillStyle = ball.color
		ctx.shadowBlur = 10
		ctx.shadowColor = ball.color
		ctx.fill()
		ctx.closePath()
		ctx.shadowBlur = 0
		ctx.shadowColor = 'transparent'
	})
}

export const drawPowerUps = (
	ctx: CanvasRenderingContext2D,
	powerUps: PowerUp[]
) => {
	powerUps.forEach(powerUp => {
		// Draw power-up background
		ctx.beginPath()
		ctx.arc(
			powerUp.x + powerUp.width / 2,
			powerUp.y + powerUp.height / 2,
			powerUp.width / 2,
			0,
			Math.PI * 2
		)
		ctx.fillStyle = powerUp.color
		ctx.shadowBlur = 20
		ctx.shadowColor = powerUp.color
		ctx.fill()
		ctx.closePath()

		// Draw power-up icon
		ctx.fillStyle = 'white'
		ctx.font = '14px Arial'
		ctx.textAlign = 'center'
		ctx.textBaseline = 'middle'

		// Get the icon from POWER_UP_CONFIG
		const icon = POWER_UP_CONFIG[powerUp.type].label.split(' ')[0] // Extract emoji icon
		ctx.fillText(
			icon,
			powerUp.x + powerUp.width / 2,
			powerUp.y + powerUp.height / 2
		)

		// Reset shadow
		ctx.shadowBlur = 0
		ctx.shadowColor = 'transparent'
	})
}
